.spaces-page {}

.spaces-container {
	&__tab-item {
		&[aria-hidden=false] {
			.spaces-btn {
				display: inline-block; } }
		&.tab-item-empty {
			.spaces-btn {
				display: inline-block;
				position: relative;
				top: 0;
				right: 0; } } }

	@include dr {
		padding: 0 15px; }
	@include m {
		padding: 0; } }

.spaces-btn {
	display: none;
	top: 27px;
	right: 34px;
	position: absolute;
	padding: 0 15px 0 15px;
	line-height: 42px;
	border: 1px solid $blue;
	border-radius: 21px;
	color: $blue;
	font-family: $rob;
	font-size: 13px;
	font-weight: 500;
	text-transform: uppercase;
	transition: color .2s ease, background .2s ease;
	vertical-align: top;
	@include m {
		position: relative;
		top: 0;
		right: 0;
		text-align: center;
		margin-bottom: 20px;
		margin-top: 10px; }
	.icon-create-space {
		width: 11px;
		vertical-align: middle;
		margin: 0 5px 3px 0;
		transition: fill .2s ease; }
	&:hover {
		background: $blue;
		color: $white;
		.icon-create-space {
			fill: $white; } } }


.spaces-btn-active {
	display: block; }

.spaces-menu {
	margin-bottom: 22px;
	line-height: 34px;
	text-align: center;
	border-bottom: 2px solid #dde6ec;

	.message {
		position: absolute;
		left: 100%;
		top: 50%;
		transform: translateY(-50%);
		width: 24px;
		height: 24px;
		font-size: 10px;
		font-style: inherit;
		color: #fff;
		border-radius: 50%;
		background: #0082f9;
		line-height: 24px;
		text-align: center; }

	li {
		width: 20%;
		position: relative;
		display: inline-block;
		line-height: normal;
		text-transform: uppercase;
		cursor: pointer;
		font-size: 14px;
		font-weight: 400;
		outline: none;
		vertical-align: top;
		@include m {
			display: block;
			width: 100%; }


		a {
			position: relative;
			color: $black-dark;
			padding: 0 10px;
			@include r(1250) {
				padding: 0 5px; }
			@include m {
				display: inline-block;
				padding: 10px; } }

		&.ui-state-active {
			a {
				display: inline-block;
				color: #00be9d;

				&:after {
					bottom: -16px;
					right: 0;
					left: 0;
					height: 3px;
					background: #00be9d;
					position: absolute;
					content: "";
					@include r(1150) {
						display: none; } } } }

		&:after {
			top: -2px;
			right: 0;
			position: absolute;
			content: "";
			width: 1px;
			height: 20px;
			background-color: #dde6ec;
			@include r(1170) {
				display: none; } }
		&:last-child {
			&:after {
				display: none; } }
		&:first-child {
			@include r(1500) {
				padding-left: 10px;
				text-align: left; }
			@include m {
				text-align: center; } }

		@include r(1300) {
			&:first-child {
				padding-left: 0; } } } }

.spaces-btn-more {
	display: block;
	margin: 34px auto 0;
	padding: 0 76px;
	background: transparent;
	line-height: 56px;
	border-radius: 30px;
	border: 1px solid #01be9d;
	font-size: 14px;
	text-transform: uppercase;
	color: $green;
	font-family: $rob;
	font-weight: 500;
	transition: color 0.2s ease, background 0.2s;
	&:hover {
		background: #01be9d;
		color: #fff; } }

.pending-items {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -1% 0;
	font-size: 0;
	@include m {
		display: block;
		margin: 0; } }

.pending-item {
	padding: 22px 28px 20px 22px;
	background: $white;
	margin: 0 1% 26px;
	width: 48%;
	display: inline-block;
	border-radius: 5px;
	box-shadow: 0 4px 10px rgba(0, 81, 115, 0.1);
	line-height: 1;
	transition: box-shadow 0.2s ease;
	@include clr;
	&:hover {
		box-shadow: 0 10px 10px rgba(0, 81, 115, 0.1); }
	@include r(1280) {
		padding: 16px 20px 15px 16px; }
	@include m {
		padding: 15px;
		display: block;
		margin:  0 auto 20px;
		width: 280px; }

	&__pic {
		width: 40%;
		margin-right: 3%;
		float: left;
		height: 100%;
		@include m {
			float: none;
			width: 100%;
			margin-bottom: 10px; }
		img {
			width: 100%;
			border-radius: 5px; } }

	&__content {
		width: 57%;
		float: left;
		@include m {
			float: none;
			width: 100%; } }

	&__head {
		border-bottom: 1px solid #dde6ec;
		@include clr; }

	&__time {
		float: left;
		.left {
			margin-bottom: 10px;
			position: relative;
			padding-left: 24px;
			text-transform: uppercase;
			color: $black;
			font-family: $rob;
			font-size: 14px;
			font-weight: 400;
			@include css-lock(11, 14, 1150, 1600, font-size);
			@include r(1280) {
				margin-bottom: 7px; }

			&:before {
				content: "";
				position: absolute;
				left: 0;
				top: -2px;
				@include s(time-ads);
				@include r(1280) {
					top: -4px; } } }

		.applied {
			margin-bottom: 12px;
			color: $black-dark;
			font-family: $rob;
			font-size: 13px;
			@include css-lock(10, 13, 1150, 1600, font-size);
			@include r(1280) {
				margin-bottom: 9px; } }

		.applied-left {
			font-weight: 700;
			text-transform: uppercase; }

		.applied-right {
			font-weight: 400;
			text-transform: uppercase; } }

	&__buttons {
		float: right;
		.approve,
		.deny {
			position: relative;
			display: inline-block;
			width: 32px;
			height: 32px;
			border-radius: 50%;
			cursor: pointer;
			@include nb {
				width: 24px;
				height: 24px; } }

		.approve {
			border: 1px solid $green;
			transition: background .2s ease;
			&:hover {
				background: #00be9d;
				.icon-check-svg {
					fill: $white; } } }
		.icon-check-svg {
			fill: $green;
			font-size: 11px;
			transition: fill .2s ease;
			@include vcenter;
			@include nb {
				font-size: 9px; } }


		.deny {
			margin-right: 10px;
			border: 1px solid #df4f62;
			transition: background .2s ease;
			&:hover {
				background: $red;
				.icon-close {
					fill: $white; } } }

		.icon-close {
			fill: $red;
			font-size: 11px;
			transition: fill .2s ease;
			@include vcenter;
			@include nb {
				font-size: 9px; } } }

	&__descr {
		border-bottom: 1px solid #dde6ec; }


	&__title {
		margin: 14px 0 6px;
		color: #333;
		font-weight: 700;
		font-family: $mont;
		font-size: 16px;
		@include css-lock(12, 16, 1150, 1600, font-size);
		@include r(1280) {
			margin: 10px 0 4px; }
		//+r(1150)
 }		///font-size: 12px

	&__subtitle {
		margin-bottom: 8px;
		display: inline-block;
		color: $blue;
		text-decoration: underline;
		font-size: 14px;
		font-weight: 700;
		font-family: $mont;
		@include css-lock(11, 14, 1150, 1600, font-size);
		@include r(1280) {
			margin-bottom: 6px; } }

	&__textual {
		color: $black;
		font-family: $rob;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.43;
		@include css-lock(11, 14, 1150, 1600, font-size);

		p {
			margin-bottom: 8px;
			@include r(1280) {
				margin-bottom: 6px; } } }

	&__links {
		margin-top: 10px;
		font-weight: 700;
		font-size: 14px;
		font-family: $mont;
		@include css-lock(11, 14, 1150, 1600, font-size);
		@include r(1280) {
			margin-top: 7px; }

		.left {
			position: relative;
			margin-right: 17px;
			text-decoration: underline;
			color: $blue;
			&:after {
				bottom: 1px;
				left: calc(100% + 8px);
				position: absolute;
				content: "/";
				color: #333;
				font-size: 12px; } }
		.right {
			text-decoration: underline;
			color: $green; } }

	&__data {
		margin-top: 6px;
		color: $black-dark;
		font-family: $rob;
		font-size: 13px;
		@include css-lock(10, 13, 1150, 1600, font-size);
		@include r(1280) {
			margin-top: 4px; }

		.descr {
			padding-right: 5px;
			text-transform: uppercase;
			font-weight: 700;

			&--bigger {
				padding-top: 26px;
				font-size: 14px;
				@include css-lock(10, 14, 1150, 1600, font-size);
				@include r(1280) {
					padding-top: 15px; } } }


		.value {
			font-weight: 400;

			&--bigger {
				font-size: 14px;
				@include css-lock(10, 14, 1150, 1600, font-size); } } } }

