.account-tab {
	padding-bottom: 80px; }
.account-nav {
	text-align: center;
	&__tablinks {
		position: relative;
		display: inline-block;
		&:after {
			content: '';
			position: absolute;
			left: 0;
			width: 100%;
			height: 2px;
			bottom: -13px;
			background: #dde6ec;
			@include m {
				display: none; } } }
	&__link {
		font-family: $rob;
		position: relative;
		display: inline-block;
		font-size: 14px;
		padding: 0 25px;
		color: #181818;
		border-right: 1px solid #dde6ec;
		transition: all 0.3s;
		text-transform: uppercase;
		cursor: pointer;
		@include m {
			border-right: none;
			display: block;
			margin-bottom: 10px; }

		&:after {
			content: '';
			position: absolute;
			bottom: -13px;
			width: 0;
			height: 3px;
			left: 0;
			background: #00be9d;
			transform: translateX(0%);
			transition: all 0.3s;
			z-index: 1;
			@include m {
				display: none; } }
		&:last-of-type {
			border-right: none; }
		&.is-active {
			color: #00be9d;
			&:after {
				width: 100%; } }
		span {
			@include m {
				&:before {
					content: '';
					position: absolute;
					top: -5px;
					right: -5px;
					bottom: -5px;
					left: -5px; } } } } }

.account-section {
	display: none;
	padding-top: 45px;
	@include m {
		padding-top: 15px; } }

.account-inner {}

.account-btn {
	display: flex;
	justify-content: space-between;
	padding-top: 10px;
	@include r(1099) {
		padding-left: 15px;
		padding-right: 15px; }
	@include m {
		display: block;
		padding-left: 0;
		padding-right: 0; } }

.field--xs {
	margin-right: 15px;
	margin-left: 10px;
	@include m {
		margin-left: 0; }
	.field__input--sm {
		padding: 17px 8px;
		width: 100px;
		text-align: center;
		@include m {
			width: 100%; } } }


.field--sm {
	.field__input--sm {
		width: 228px;
		@include m {
			width: 100%; } } }

.field__text-s {
	display: inline-block;
	padding-top: 38px;
	@include m {
		display: block;
		text-align: center; }
	p {
		font-family: $mont;
		font-size: 14px;
		color: #313131;
		font-weight: bold;
		text-transform: uppercase; }
	span {
		font-size: 22px; } }

.fields_space {
	padding-bottom: 16px; }
