.view-medium {
	.two-col__left {
		@include dr {
			width: 300px; }
		@include m {
			margin: 0 auto 20px;
			width: auto; } }
	.spacel__slide {
		@include dr {
			height: 300px; } }
	.two-col__right {

		@include dr {
			margin-left: 315px; }
		@include m {
			margin-left: 0; } }
	.profile__tumb-slide {
		@include dr {
			width: 100px;
			height: 78px; } }
	.profile__right {
		padding: 22px 15px;
		@include m {
			padding: 0; } }
	.pblock__img {
		@include r(1250) {
			height: 165px; } }
	&__eddit-btn {
		position: absolute;
		top: -52px;
		right: 15px; } }
