
.field {
	display: inline-block;
	margin-right: 30px;
	vertical-align: top;
	//margin-bottom: 10px
	position: relative;
	@include clr;
	@include m {
		display: block;
		margin-right: 0; }
	.select-view {
		height: 53px; }
	&__title {
		color: $black-th;
		font-size: 13px;
		font-weight: 500;
		line-height: 2.15;
		text-transform: uppercase;
		margin-bottom: 6px; }
	&__input, &__textarea, .select-view {
		background-color: $field-bg;
		//background-color: red
		font-size: 15px;
		font-weight: 400;
		line-height: 1;
		//color: $black-th
		appearance: none;
		letter-spacing: -0.5px;
		//margin-bottom: 10px
		border: 1px solid $field-bg;
		@include trans;
		//position: relative
		@include placeholder {
			//color: $placeholder-color
 }			//font-size: 14px
		@include m {
			background-color: $field-bg-m;
			border: 1px solid $field-bg-m; } }
	&__input, &__textarea {
		padding: 17px 22px;
		vertical-align: top; }
	&__input {
		border-radius: 27px;
		display: inline-block;
		@include m {
			display: block;
			width: 100%; }
		&--full {
			width: 100%; }
		&--lg {
			width: 431px; }
		&--md {
			width: 397px; }
		&--m {
			width: 368px; }
		&--ml {
			width: 364px; }
		&--s {
			width: 322px; }
		&--sl {
			width: 228px; }
		&--sm {
			width: 202px; }
		&--xs {
			width: 168px;
			height: 35px;
			padding: 5px 10px; }
		&--check {
			display: none;
			&:checked + div.field__input-radio-icon {
				&:before {
					display: block; }
				svg {
					display: block; } } }
		&--negative-pos {
			position: relative;
			top: -6px; }
		&--marg-b {
			margin-bottom: 10px; }
		&--curs-pointer {
			cursor: pointer; } }
	&__textarea {
		display: block;
		border-radius: 8px;
		resize: none;
		min-height: 78px;
		//border: none
		width: 100%;
		@include xs {
			min-height: 100px; } }
	&__add-field {
		color: $green;
		font-size: 14px;
		line-height: 2;
		text-decoration: underline;
		appearance: none;
		background: none;
		height: 51px;
		padding: 10px 22px 10px 40px;
		position: relative;
		margin-left: 25px;
		@include m {
			margin-bottom: 10px;
			height: auto; }
		&:before {
			content: '+';
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			width: 27px;
			height: 27px;
			border: 1px solid $green;
			line-height: 24px;
			border-radius: 50%; } }
	&__wrapper {
		//+clr
		position: relative;
		margin-bottom: 10px; }

	&__calendar-wrapper {
		&.is-open {
			.dropbox {
				display: block; } } }
	&__input-radio-icon {
		position: absolute;
		width: 20px;
		height: 20px;
		opacity: 0.75;
		border-radius: 4px;
		background-color: #e9eaea;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		text-align: center;
		padding-top: 5px;
		padding-left: 3px;
		&:before {
			content: '';
			@include coverdiv;
			background-color: $green;
			display: none;
			border-radius: 4px; }
		svg {
			fill: $white;
			font-size: 10px;
			line-height: 1;
			z-index: 1;
			position: relative;
			display: none;

 }			//display: none
		//&:after
		//	content: ''
		//	+s(check-white)
		//	+vcenter
 }		//	display: none
	&__radio-inner {
		width: 165px;
		padding-right: 20px;
		display: inline-block;
		vertical-align: top;
		margin-bottom: 18px;
		position: relative;
		&--special {
			width: 84px;
			@include m {
				display: inline-block; } } }
	&__radio-wrapper {
		max-width: 336px;
		display: inline-block;
		margin-top: 5px;
		vertical-align: top; }
	&--full {
		display: block;
		width: 100%; }
	&--check {
		//margin-bottom: -18px
		padding-bottom: 10px;
		label {
			color: $black;
			font-size: 14px;
			line-height: 1.7;
			display: block;
			position: relative;
			padding-left: 32px;
			cursor: pointer;
			&:before {
				content: '';
				position: absolute;
				top: -5px;
				right: -5px;
				bottom: -5px;
				left: -5px; } } }
	&--full {
		display: block;
		margin-right: 0; }
	&--select-big {
		.select_wrapper {
			width: 397px; } }
	&--multiple {
		.plus, .location-icon {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: 20px;
			@include s(input-plus);
			cursor: pointer;
			&:before {
				content: '';
				position: absolute;
				top: -8px;
				bottom: -8px;
				right: -8px;
				left: -8px; } }
		.location-icon {
			@include s(location-icon); } }
	&__hidden {
		display: none; }
	&__date-droplist {
		top: 120%; }
	small {
		border-radius: 5px;
		font-size: 11px;
		position: absolute;
		top: 50%;
		//transform: translateY(-50%)
		background: $red;
		color: $white;
		right: 50px;
		padding: 9px;
		display: none;
		margin-top: -15px;
		line-height: 1;
		font-weight: 100;
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			//transform: translateY(-50%)
			margin-top: -12px;
			right: -9px;
			@include arr(10, 24, $red, r); } } }

$select-color: #a7a7a8;
$select-bg-not: #fff;
$select-border-color: #e2e2e2;
$select-active-li: #eaeaea;
$select-hover-li: #f9f9f9;
$select_wrapper-width: 200px;
.select-view {
	cursor: pointer;
	position: relative;
	border-radius: 27px;
	padding: 18px 35px 18px 22px;
	overflow: hidden;
	border: 1px solid $field-bg;
	background-color: $field-bg;
	font-size: 14px;
	width: 100%;
	// min-width: 228px
	// min-height: 52px
	//word-wrap: normal

	margin-bottom: 10px;
	&:after {
		@include trans;
		content: '';
		position: absolute;
		right: 20px;
		top: 46%;
		@include s(field-arrow); } }
.select_wrapper {
	width: 228px;
	position: relative;
	min-height: 51px;
	.active {
		&:after {
			transform: rotateX(180deg); } }
	@include m {
		width: 100%!important; } }
.select_placeholder {
	color: $placeholder-color !important; }
ul.select-options {
	background: $select-bg-not;
	position: absolute;
	display: none;
	cursor: pointer;
	border: 1px solid #e2e2e2;
	z-index: 1;
	width: 100%;
	border-radius: 5px;
	// height: 200px
	// overflow-y: scroll
	li {
		font-size: 14px;
		padding: 5px 10px 5px 18px;
		transition: background .4s;
		&[rel="hide"] {
			display: none; }
		&.active {
			background: $select-active-li;
			&:hover {
				background: $select-active-li; } }
		&:hover {
			background: $select-hover-li; } } }
.select-hidden {
	display: none; }
.error-icon {
	width: 30px;
	height: 30px;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	background: $red;
	right: 7px;
	color: $white;
	font-size: 20px;
	text-align: center;
	border-radius: 50%;
	line-height: 30px;
	display: none;
	&:hover + small {
		display: block; } }
.field_with_errors {
	input, textarea, .select-view {
		border-color: $red; }  // for popup select
	.error-icon {
		display: block; } }
.upload__item {
	&.error {
		border-color: $red; } }
.error-content {
	display: inline-block;
	margin-left: 20px;
	position: relative;
	z-index: 2;
	top: -5px;
	.error-icon {
		left: 0;
		top: 20%;
		right: auto;
		display: block; }
	small {
		border-radius: 5px;
		font-size: 11px;
		position: absolute;
		top: 50%;
		background: $red;
		color: $white;
		right: -180px;
		padding: 9px;
		display: none;
		margin-top: -15px;
		min-width: 120px;
		line-height: 1;
		text-transform: none;
		font-weight: 100;
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			margin-top: -12px;
			right: auto;
			left: -9px;
			@include arr(10, 24, $red, l); } } }
