.header {
	position: relative;
	//&.header-inner-page
	///min-height: 552px
	margin-bottom: 80px;
	@include m {
		margin-bottom: 55px; }
	.topline-in-pages {
		position: relative;
		background-image: linear-gradient(to right, #004ea5 0%, #00a88a 100%);
		//z-index: 1
		@include xs {
			//padding-top: 11px
			//padding-bottom: 1px
			padding: 5px 0; }
		//&:after
		//	content: ''
		//	+coverdiv
		//	opacity: 0.9
		//	box-shadow: 0 12px 32px rgba(0, 0, 0, 0.15)
 }		//	z-index: -1
	&__topline {
		padding-top: 5px;
		padding-bottom: 5px;
		@include xs {
			padding-top: 27px; } }
	.container {
		opacity: 1;
		@include clr; }
	&__main-cont {
		min-height: 400px;
		padding-bottom: 40px;
		text-align: center;
		position: relative;
		@include m {
			min-height: 254px; }
		&--more {
			.header__vertical {
				padding-top: 20px;
				@include m {
					@include vertical;
 }					//height: auto
				@include xs {
					height: auto;
					padding-top: 40px; } }
			.title-lg {
				@include m {
					line-height: 1.25; } }
			.header__text {
				@include m {
					margin-top: 7px; } } } }
	&__vertical {
		//height: 370px
		height: 330px;
		padding-top: 20px;
		@include rmin(992) {
			@include vertical; }
		@include m {
			padding-top: 70px;
			height: 159px;
 } }			//height: auto
	&__inner {
		display: inline-block;
		vertical-align: middle; }
	&__text {
		color: $white;
		font-size: 14px;
		line-height: 1.57;
		margin-top: 40px; }
	&__bg {
		position: absolute;
		@include coverdiv;
		background-position: 50% 0%;
		background-repeat: no-repeat;
		z-index: -2; }
	.hboard {
		display: none; }
	&__left {
		float: left;
		//+xs
		//	width: 140px
 }		//	height: 45px
	&__logo {
		width: 164px;
		height: 54px;
		a {
			display: block;
			height: 100%; }
		img {
			width: 100%;
			height: 100%;
			object-fit: contain; } }
	&__right {
		float: right;
		font-size: 0;
		margin-top: 3px;
		@include m {
			display: none; }
		//&.header-adm__right
 }		//	margin-top: 0
	&__menu {
		display: inline-block;
		position: relative;
		margin-right: 47px;
		&:after {
			height: 14px;
			width: 1px;
			content: '';
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: -38px;
			background: $white; }
		a {
			color: $white-darken;
			font-size: 15px;
			line-height: 1;
			margin-right: 59px;
			position: relative;
			&:last-of-type {
				margin-right: 0; }
			@include mmin {
				&:after {
					content: '';
					position: absolute;
					top: -8px;
					right: -8px;
					bottom: -8px;
					left: -8px; } } } }

	&__burger {
		float: right;
		//margin-top: 12px
		margin-top: 18px; }
	&:after {
		content: '';
		z-index: -1;
		@include coverdiv;
		opacity: 0.65;
		background-image: linear-gradient(to top, #242d36 0%, rgba(36, 45, 54, 0.55) 100%); }
	&__scrollto {
		position: absolute;
		left: 50%;
		transform: translate(-50%, 50%);
		bottom: 0;
		width: 66px;
		height: 66px;
		background-color: $white;
		border: 1px solid $white;
		box-shadow: 0 6px 22px rgba(0, 0, 0, 0.15);
		z-index: 2;
		border-radius: 50%;
		&:before {
			content: '';
			@include vcenter;
			@include s(scrollto); } }
	&__breadcrumbs {
		position: absolute;
		left: 11px;
		bottom: 40px;
		@include m {
			position: relative;
			bottom: 0;
			left: 0;
			//margin-top: 40px
			margin-top: 20px; } } }
.header-home {
	margin-bottom: 0;
	.header__bg {
		background-position: 50% 50%; }
	.header__burger {
		margin-top: 12px; }
	.hboard {
		display: block;
		position: absolute;
		bottom: 0;
		width: 360px;
		height: 676px;
		right: 15.5%;
		background-size: contain;
		background-repeat: no-repeat;
		z-index: -1;
		@include r(1549) {
			height: 550px;
			right: 10%; }
		@include m {
			right: 25%;
			transform: translateX(50%);
			height: 370px;
			width: 200px; }
		@include xs {
			width: 150px;
			height: 250px; } } }
.header-adm {
	margin-bottom: 0;
	@include m {
		margin-bottom: 0; }
	.topline-in-pages {
		padding: 0;
		@include m {
			padding-top: 5px;
			padding-bottom: 5px; }
		//+xs
 }		//	padding-bottom: 20px
	&__cont {
		@include clr;
		//+m
		//	padding-left: 15px
 }		//	padding-right: 15px
	&__menu {
		vertical-align: top;
		margin-top: 35px; }
	&__logo {
		width: 134px;
		height: 46px;
		@include xs {
			display: block; } }
	&__left {
		display: none;
		@include m {
			display: block;
			width: auto;
			background: none; }
		@include xs {
			padding-top: 0px;
			padding-bottom: 0px; } }
	&__right {
		margin-top: 0; }
	&__menu {
		@include m {
			border-bottom: 1px solid $border-gray;
			padding-bottom: 25px;
			margin-bottom: 28px; }
		&:after {
			display: none; }
		a {
			@include m {
				margin-bottom: 0; } } }
	&__burger {
		margin-top: 12px;
		@include xs {
			margin-top: 12px; } }
	&--full {
		.header-adm__left {
			display: block; }
		.header-adm__logo {
			width: 184px;
			height: 54px;
			@include m {
				width: 134px; } }

		.header__left {
			margin-top: 13px;
			margin-left: 30px;
			@include m {
				margin-top: 0;
				margin-left: 0; } } } }
.header-small {
	margin-bottom: 0; }
.hslider-container {
	margin-top: 183px;
	padding-bottom: 80px;
	@include r(1549) {
		margin-top: 56px;
		padding-bottom: 105px; } }
.hslider {
	padding-bottom: 125px;
	@include m {
		padding-bottom: 0; }
	&__title {
		margin-bottom: 39px;
		@include m {
			margin-bottom: 25px; } }
	&__text {
		font-size: 22px;
		color: $white-light;
		margin-bottom: 27px;
		@include m;
		line-height: 1.5; }
	&__slide {
		padding: 0 2px;
		@include m {
			display: none;
			text-align: center;
			&--mob-show {
				display: block; } } }
	.custom-prev, .custom-next {
		bottom: 0; }
	.custom-prev {
		right: 71px; }
	.custom-next {
		right: 0; } }
.nav-respons {
	text-align: center;
	display: none;
	opacity: 0.95;
	padding: 30px 25px;
	@include m {
		z-index: 2222;
		position: fixed;
		top: 0;
		left: 0;
		width: 100vw;
		height: 100vh;
		overflow: scroll;
		background-image: linear-gradient(45deg, #01be9d 0%, #0057b8 100%); }
	.header__left {
		float: none;
		display: inline-block; }
	.header__right {
		float: none;
		display: block;
		margin-top: 0; }
	.header__menu {
		display: block;
		margin-right: 0;
		padding-bottom: 15px;
		margin-bottom: 35px;
		&:after {
			bottom: 0;
			width: 100%;
			height: 1px;
			left: 0;
			right: auto;
			top: auto; }
		a {
			display: block;
			margin-right: 0;
			font-size: 22px;
			padding-top: 10px;
			padding-bottom: 10px;
			margin-bottom: 20px; } }
	.user {
		display: block;
		&__sign {
			border: 2px solid white;
			border-radius: 21px; } }
	&__burger {
		margin-top: 20px;
		margin-bottom: 45px; } }

