.btn-switch {
  font-size: 9px;
  position: relative;
  display: inline-block;
  @include nosel; }


.btn-switch__radio {
  display: none; }

.btn-switch__label {
  display: inline-block;
  padding: .75em .1em .75em .8em;
  vertical-align: top;
  font-size: 1em;
  font-weight: 700;
  line-height: 1.5;
  color: #666;
  cursor: pointer;
  transition: color .2s ease-in-out;
  //width: 50px
  + .btn-switch__label {

    padding-right: .75em;
    padding-left: 0; } }
.btn-switch__txt {
  position: relative;
  z-index: 2;
  display: inline-block;
  //min-width: 1.5em
  opacity: 1;
  pointer-events: none;
  transition: opacity .2s ease-in-out;
  text-transform: uppercase;
  color: $white;
  line-height: 1; }

.btn-switch__radio_bus:checked ~ .btn-switch__label_pers .btn-switch__txt, .btn-switch__radio_pers:checked ~ .btn-switch__label_bus .btn-switch__txt {
  opacity: 0; }

.btn-switch__label:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: $green;
  border-radius: 1.5em;
  box-shadow: inset 0 0.0715em 0.3572em rgba(43, 43, 43, 0.05);
  transition: background .2s ease-in-out; }

.btn-switch__radio_pers:checked ~ .btn-switch__label:before {
  background: $blue; }

.btn-switch__label_bus:after {
  content: "";
  position: absolute;
  z-index: 2;
  top: .5em;
  bottom: .5em;
  left: .5em;
  width: 2em;
  background: #fff;
  border-radius: 1em;
  pointer-events: none;
  //box-shadow: 0 0.1429em 0.2143em rgba(43, 43, 43, 0.2), 0 0.3572em 0.3572em rgba(43, 43, 43, 0.1)
  transition: left .2s ease-in-out, background .2s ease-in-out; }

.btn-switch__radio_pers:checked ~ .btn-switch__label_bus:after {
  left: calc(100% - 2.5em);
  background: #fff; }

.btn-switch__radio_bus:checked ~ .btn-switch__label_pers:before {
  z-index: 1; }

.btn-switch__radio_pers:checked ~ {
  .btn-switch__label_bus:before {
    z-index: 1; }
  .btn-switch__label_pers {
    color: #fff; } }
