.container {
	max-width: 1200px;
	padding: 0 15px;
	position: relative;
	margin: 0 auto;
	&--sm {
		max-width: 800px; }
	&--m {
		max-width: 820px;
		padding: 0; }
	&--xs {
		max-width: 700px; }
	&--full {
		max-width: 100%;
		padding: 0;
		@include clr;
		@include r(991) {
			padding:  0 20px; } } }
