.create-account {
	&__tabs {
		padding-top: 17px;
		.soc {
			position: absolute;
			top: 7px;
			left: -2px;
			padding: 0;
			width: auto;
			background: inherit;
			border-right: none; } }
	.fields_b {
		padding-bottom: 0; } }
.create-acc-nav {
	padding: 4px 0 6px 0;
	&__tablinks {}

	&__link {
		position: relative;
		display: inline-block;
		padding-left: 30px;
		font-size: 14px;
		color: #585858;
		&:first-of-type {
			margin-right: 25px; }
		&:before {
			content: '';
			position: absolute;
			width: 20px;
			height: 20px;
			border-radius: 50%;
			background: #efefef;
			left: 0;
			top: 1px; }
		&.is-active {
			&:before {
				background: #00be9d; }
			&:after {
				content: '';
				position: absolute;
				width: 10px;
				height: 10px;
				border-radius: 50%;
				background: #fff;
				left: 5px;
				top: 6px; } }

		span {} } }
.create-acc {
	&__section {
		display: none;
		.field__title {
			span {
				text-transform: capitalize; } } } }
