.article-page {
	padding-top: 5px;
	margin-bottom: 86px;
	@include m {
		margin-bottom: 40px; }
	&__wrapper {
		@include clr; }
	&__main-cont {
		float: left;
		//width: percentage(870/1170)
		width: calc(100% - 300px);
		@include m {
			width: 100%;
			float: none; }
		p {
			margin-bottom: 25px; }
		&>img {
			width: 100%;
			height: auto;
			margin-bottom: 32px; } }
	&__sidebar {
		float: right;
		@include m {
			float: none; } }
	&__nav {
		border-top: 1px solid $border-gray;
		padding-top: 40px; } }
.sidebar {
	padding-top: 5px;
	width: 270px;
	@include m {
		width: 100%; }
	&__title {
		line-height: 1;
		margin-bottom: 25px; }
	&__post {
		&:last-of-type {
			margin-bottom: 0;
			padding-bottom: 0;
			border-bottom: none; } } }
.page-nav {
	font-size: 0;
	border-top: 1px solid $border-gray;
	padding-top: 39px;
	@include m {
		margin-bottom: 34px;
		padding-top: 20px;
		padding-bottom: 20px;
		border-bottom: 1px solid $border-gray; }
	&__left, &__right {
		display: inline-block;
		width: 50%;
		h4 {
			color: $black-dark;
			font-family: $mont;
			font-size: 15px;
			font-weight: 700;
			line-height: 1.22;
			text-transform: uppercase;
			@include m {
				display: none; } } }
	&__left {
		padding-right: 20px;
		h4 {
			padding-left: 17px; } }
	&__right {
		padding-left: 20px;
		text-align: right;
		h4 {
			padding-right: 17px; } }
	&__link {
		font-size: 13px;
		font-weight: 500;
		line-height: 1;
		position: relative;
		text-transform: uppercase;
		margin-bottom: 5px;
		display: inline-block;
		&:before, &:after {
			content: '';
			position: absolute; }
		&:before {
			top: 50%;
			transform: translateY(-50%); }
		&:after {
			top: -8px;
			bottom: -8px;
			right: -8px;
			left: -8px; }
		&--prev {
			padding-left: 17px;
			&:before {
				@include s(prev2);
				left: 0px; } }

		&--next {
			padding-right: 17px;
			&:before {
				@include s(next2);
				right: 0px; } } } }
