.burger {
	display: none;
	@include r(991) {
		display: inline-block; }
	&__icon {
		position: relative;
		width: 28px;
		height: 20px;
		overflow: visible;
		&:before, &:after {
			@include trans;
			content: '';
			position: absolute;
			height: 3px;
			width: 28px;
			background: white;
			left: 0; }
		&:before {
			top: 0px;
			transform-origin: top left; }
		&:after {
			bottom: 0px;
			transform-origin: bottom left; }
		div {
			height: 3px;
			width: 28px;
			z-index: 2;
			background: white;
			position: absolute;
			top: 50%;
			margin-top: -2px;
			@include trans; }
		&--close {
			@include r(991) {
				margin-left: auto;
				margin-right: auto;
				margin-bottom: 10px;
				div {
					opacity: 0; }
				&:before, &:after {
					width: 25px; }
				&:before {
					transform: translateX(6px) rotate(45deg); }
				&:after {
					transform: translateX(6px) rotate(-45deg); } } } }
	&__text {
		color: $white-light;
		line-height: 1; } }
