.sel-method {

	&__block {
		border-bottom: 1px dashed  #d4dce1;
		padding-bottom: 16px;
		margin-bottom: 24px;
		&--border-full {
			border-bottom: 1px solid  #d4dce1; } }
	&__top-left, &__top-right {
		display: inline-block;
		vertical-align: top;
		@include m {
			display: block;
			width: 100%;
			text-align: center; } }
	&__top-left {
		width: 75%; }
	&__top-right {
		width: 25%;
		text-align: right; }
	&__container {
		padding: 29px;
		font-size: 0;
		border-radius: 5px;
		box-shadow: 0 6px 22px rgba(123, 139, 149, 0.35);
		background: $white;
		margin-bottom: 50px;
		// +r(1099)
		// 	margin-right: 15px
		// 	margin-left: 15px
		@include m {
			margin-right: auto;
			margin-left: auto;
			background: none;
			box-shadow: none;
			border-radius: 0; } }
	&__block-title {
		color: #333;
		font-size: 13px;
		font-weight: 500;
		text-transform: uppercase;
		line-height: 1;
		margin-bottom: 5px; }
	&__sec-title {
		color: $black-sec;
		font-family: $mont;
		font-size: 18px;
		font-weight: 900;
		text-transform: uppercase;
		a {
			color: $green;
			text-decoration: underline;
			&:hover {
				text-decoration: none; } } }
	&__cost, &__right-text {
		color: $black-sec;
		font-family: $mont;
		font-weight: 700;
		line-height: 1;
		text-transform: uppercase;
		font-size: 22px; }
	&__right-text {
		font-size: 14px; }
	&__text-date {
		color: #333;
		font-size: 13px;
		line-height: 1.7;
		font-weight: 400;
		margin-bottom: 20px; }
	&__sep {
		color: #333;
		font-size: 13px;
		font-weight: 500;
		line-height: 2.15;
		display: inline-block;
		vertical-align: top;
		margin: 12px 15px 0; }
	.select_wrapper {
		min-width: 270px; }
	.select-options {
		height: 100px;
		overflow: scroll; }
	&__select {
		margin-right: 0; }
	//&__select, .field
	//	padding-top: 14px
	//	padding-bottom: 15px
	&__field {
		width: 270px;
		margin-right: 15px;
		@include m {
			width: 100%; }
		input {
			width: 100%;
			padding: 17px 22px; }
		&--last {
			margin-right: 0; }
		&--small {
			input {
				width: 168px;
				@include m {
					width: 100%; } } } }
	&__upload-label {
		border: 1px solid $green;
		border-radius: 28px;
		text-align: center;
		color: $green;
		font-size: 13px;
		font-weight: 400;
		line-height: 53px;
		text-transform: uppercase;
		width: 160px;
		height: 53px;
		cursor: pointer;
		vertical-align: top;
		display: inline-block;
		@include m {
			display: block;
			margin:  0 auto 0px; } }
	&__bottom {
		text-align: center;
		padding-top: 12px; }
	&__botom-text {
		color: #313131;
		font-family: $black-sec;
		font-weight: 700;
		line-height: 1.5;
		text-transform: uppercase;
		font-size: 18px;
		margin-bottom: 5px; }
	&__error-text {
		color: $red;
		font-size: 13px;
		font-weight: 400;
		line-height: 1.5; }
	&__btn {
		padding: 20px 32px;
		display: block;
		margin-left: auto;
		margin-right: auto; } }

