.footer {
	border-top: 1px solid $border-gray;
	padding-top: 45px;
	@include m {
		padding-top: 38px; }
	&__wrapper {
		font-size: 0;
		padding-bottom: 38px;
		@include m {
			text-align: center; } }
	&__block-title {
		color: $black-sec;
		font-family: $mont;
		font-size: 15px;
		font-weight: 700;
		line-height: 1;
		text-transform: uppercase;
		margin-bottom: 19px;
		display: inline-block; }
	&__about, &__center, &__socials {
		display: inline-block;
		vertical-align: top; }
	&__about {
		width: percentage(468/1170);
		@include m {
			display: block;
			width: 100%;
			margin-bottom: 20px; }
		@include xs {
			display: none; }
		p {
			color: $gray-dark;
			font-size: 14px;
			line-height: 1.71; } }
	&__center, &__socials {
		@include xs {
			display: block;
			width: 100%;
			margin-left: 0;
			margin-right: 0;
			text-align: center; } }
	&__center {
		margin-left: 7%;
		@include r(1099) {
			margin-left: 4%; }
		@include xs {
			margin-bottom: 20px;
			@include clr; } }
	&__menu {
		margin: 0; }
	&__menu-block {
		display: inline-block;
		vertical-align: top;
		margin-right: 92px;
		text-align: left;
		@include xs {
			display: inline-block;
			max-width: 140px;
			margin-right: 50px; }
		&--last {
			margin-right: 0;
			@include xs {
				max-width: 100px;
				margin-right: 0; } }
		@include r(1099) {
			margin-right: 60px;
			&--last {
				margin-right: 0; } } }
	&__menu-item {
		a {
			color: $gray-dark;
			font-size: 14px;
			line-height: 1.71; } }
	&__socials {
		margin-left: 7%;
		text-align: left;
		.footer__block-title {
			margin-bottom: 24px; } }
	&__copyright {
		border-top: 1px solid $border-gray;
		color: $gray-light;
		font-size: 14px;
		line-height: 1.7;
		text-align: center;
		padding: 23px 0; } }
