// header
.origin__logo {
  margin: 12px 100px;
  width: 186px;
  height: 54px; }

// components/_field.sass
.field_with_errors {
  .field__input-radio-icon {
    border: 1px solid #e63950; } }

// app/assets/stylesheets/page-content/_myAds.sass
.adstnav {
  &__link {
    width: 25%; // to hide target_markets tab for dashboard ads pages
    a {
      color: #181818;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        bottom: -17px;
        width: 0;
        height: 3px;
        left: 0;
        background: #00be9d;
        transform: translateX(0%);
        transition: all 0.3s;
        z-index: 1; } }
    &.is-active {
      a {
        color: #00be9d;
        &:after {
          width: 100%; } } } } }

// app/assets/stylesheets/page-content/_my-account.sass
.account-nav {
  &__link {
    a {
      color: #181818;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        bottom: -17px;
        width: 0;
        height: 3px;
        left: 0;
        background: #00be9d;
        transform: translateX(0%);
        transition: all 0.3s;
        z-index: 1; } }
    &.is-active {
      a {
        color: #00be9d;
        &:after {
          width: 100%; } } } } }

// page-content/_space-listing-content.sass
.scalendar {
  td {
    a, span {
      &.datepick-selected {
        background: #0082f9 !important; }
      &.enabled {
        color: #fff;
        background: #00be9d; } } } }

