.popup-spaces {
	.wrapper {
		padding: 0;
		@include vcenter;
		@include dl {
			width: 482px; } }

	&__head {
		position: relative;
		background: #fff;
		padding: 11px 28px;
		border-radius: 10px 10px 0 0;
		border-bottom: 1px solid #dde6ec;
		@include clr; }

	&__close {
		position: absolute;
		top: 0;
		right: 28px;
		margin-top: 12px;
		cursor: pointer;
		.icon-close {
			width: 18px;
			height: 18px;
			fill: #cbcbcb; } }

	&__name {
		padding-right: 25px;
		color: $black-sec;
		text-align: left;
		text-transform: uppercase;
		line-height: 1.1;
		font-family: $mont;
		font-size: 20px;
		font-weight: 900; }

	&__content {
		@include vcenter; }

	&__descr {
		padding: 42px 55px 48px; }

	&__title {
		margin-bottom: 20px;
		line-height: 28px;
		color: $black;
		font-weight: 700;
		text-transform: uppercase;
		font-family: $rob;
		font-size: 22px; }

	&__textual {
		margin: 0 auto;
		max-width: 70%;
		color: $black;
		line-height: 28px;
		font-family: $rob;
		font-size: 18px;
		font-weight: 400;
		&--wide {
			max-width: 100%; }
		a {
			color: $green;
			text-decoration: underline; } }

	&__buttons {
		margin-top: 45px;
		.btn {
			width: 156px;
			height: 58px;
			border-radius: 29px;
			text-transform: uppercase;
			color: $white;
			font-size: 15px;
			font-weight: 500;
			font-family: $rob; }

		.btn-approve {
			margin-right: 18px;
			background-color: $green;
			box-shadow: 0 6px 22px rgba(0, 132, 109, 0.35);
			&:hover {
				box-shadow: 0 6px 22px rgba(0, 132, 109, 0.55); } }

		.btn-decline {
			margin-left: 18px;
			background-color: $red;
			box-shadow: 0 6px 22px rgba(230, 57, 80, 0.35);
			&:hover {
				box-shadow: 0 6px 22px rgba(230, 57, 80, 0.55); } } } }
