.breadcrumbs {
	font-size: 0;
	a {
		font-size: 14px;
		color: $green;
		line-height: 1;
		display: block;
		position: relative;
		padding: 5px;
		//&:after
		//	content: ''
		//	position: absolute
		//	top: -8px
		//	bottom: -8px
		//	left: -8px
		//	right: -8px
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: -18px;
			@include s(bread-arrow); } }
	li {
		display: inline-block;
		position: relative;
		margin-right: 32px;
		@include m {
			margin-bottom: 10px; }
		&:first-of-type {
			a {
				padding-left: 30px;
				&:before {
					content: '';
					position: absolute;
					@include s(bread-home);
					left: 5px;
					top: 5px; } } }
		&:last-of-type {
			margin-right: 0;
			a {
				color: $white-sec;
				&:after {
					display: none; } } } } }
.adm-breadcrumbs {
	padding-top: 37px;
	padding-bottom: 24px;
	@include m {
		padding-top: 19px;
		padding-bottom: 5px;
		border-bottom: 1px solid #dde6ec; }
	a {
		&:after {
			@include s(bread-arrow-dark); } }
	li {
		&:last-of-type {
			a {
				color: $gray-sec; } } } }
