.mp {
	&__wrapper {
		@include clr; }
	&__main-content {
		font-size: 16px;
		padding-left: 300px;
		//float: right
		//width: 868*100/1170 + %
		@include m {
			padding-left: 0; } }
	&__sidebar {
		float: left;
		@include m {
			margin: 0 auto 20px; } }
	&__main-content, &__sidebar {
		@include m {
			float: none; } }
	&__tabs {
		margin-top: 12px;
		margin-bottom: 70px; }
	&__tab {
		width: 100%; }
	&__tab-content {
		display: flex;
		flex-wrap: wrap;
		text-align: center;

		@include m {
			justify-content: center; }
		@include xs {
			display: block; } }
	.adm-breadcrumbs {
		padding-top: 29px;
		padding-bottom: 17px;
		@include m {
			border: none; } }
	&__main-title {
		margin-bottom: 11px; }
	&__tabs-topline {
		padding: 20px 14px;
		border-radius: 5px;
		box-shadow: 0 6px 22px rgba(123, 139, 149, 0.35);
		background: $white;
		display: flex;
		margin-bottom: 30px;
		@include r(1139) {
			align-items: center; }
		@include m {
			display: block; } }
	&__tabs-nav {
		flex-grow: 1;
		flex-shrink: 0;
		order: 1;
		text-align: center;
		font-size: 0; }
	&__tabs-nav-item {
		display: inline-block;
		vertical-align: top;
		margin-right: 8px;
		&--last {
			margin-right: 0; }
		@include m {
			display: block;
			max-width: 270px;
			margin: 0px auto 10px; }
		&.ui-tabs-active {
			.mp-spec-view {
				display: block; }
			.mp__tabs-nav-link--spec {
				display: none; }
			.mp__tabs-nav-link {
				background: $green;
				color: $white;
				&--blue {
					background: $blue; } } }
		&.item-full {
			.mp__tabs-nav-link {
				&--blue {
					//width: 160px
					line-height: 30px;
					br {
						display: none; } } } } }
	&__tabs-nav-link {
		border: 1px solid $green;
		padding: 7px 20px;
		//width: 160px
		color: $green;
		font-size: 13px;
		line-height: 1.2;
		text-transform: uppercase;
		border-radius: 23px;
		display: block;
		min-height: 46px;
		text-align: center;
		transition: background .2s linear;
		transition: color .2s linear;
		&--blue {
			//width: 128px
			color: $blue;
			border-color: $blue; } }
	&__prod {
		margin-right: 3.33%;
		flex-basis: 31%;
		&:nth-of-type(3n) {
			margin-right: 0; }
		@include m {
			flex-basis: 34%;
			margin-right: 0;
			&:nth-of-type(odd) {
				margin-right: 2%; } }
		@include xs {
			&:nth-of-type(odd) {
				margin-right: auto; }
			width: 100%; } }

	&__more-btn {
		margin-top: 15px; } }
.mp-spec-view {
	flex-grow: 0;
	// flex-basis: 530px
	height: 46px;
	flex-shrink: 0;
	order: 0;
	font-size: 0;
	@include r(1139) {
		flex-basis: 250px;
		height: auto;
		.select-view {
			margin-bottom: 0; } }

	&__checkblock, &__selects {
		display: inline-block;
		vertical-align: top;

		@include m {
			display: block; }
		.field {
			@include m {
				display: inline-block; } } }
	&__selects {
		@include r(1139) {
			margin-top: 15px; }
		@include m {
			max-width: 270px;
			margin: 0 auto; } }
	&__checkblock {
		margin-right: 9px;
		position: relative;
		@include dt {
			margin-right: 0; }
		@include m {
			text-align: center;
			margin: 10px 0; }
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			width: 1px;
			height: 22px;
			background-color: #d4dce1;
			right: 16px;
			@include dt {
				right: 10px; }
			@include r(1139) {
				display: none; } } }
	&--hidden {
		display: none; }
	.field {
		padding-bottom: 0;
		margin-right: 0;
		margin-top: 11px; }
	.field__radio-inner {
		margin-bottom: 0;
		width: 130px;
		padding-right: 0;
		margin-right: 9px;
		@include dt {
			width: 115px; } }
	.select_wrapper {
		//width: 106px
		//height: 46px
		width: 107px;
		display: inline-block;
		margin-right: 0;
		vertical-align: top;
		margin-right: 12px;
		.select-view {
			margin-right: 0; }
		&:last-of-type {

			margin-right: 0; }
		@include m {
			margin-right: 0;
			margin-bottom: 10px;
			&:last-of-type {
				margin-bottom: 0; } } }


	.select-view {
		padding: 10px 34px 10px 17px;
		height: 46px;
		line-height: 2;
		width: 107px;
		display: inline-block;
		margin-right: 12px;
		&:after {
			right: 15px; } }
	ul.select-options {
		width: 150px;
		@include m {
			width: 100%; } }
	label {
		line-height: 1;
		@include nosel; } }

.mp-prod {
	width: 31%;
	background-color: $white;
	border-radius: 5px;
	box-shadow: 0 4px 10px rgba(0, 81, 115, 0.1);
	overflow: hidden;
	// /max-width: 270px
	display: block;
	margin-bottom: 25px;
	vertical-align: top;
	position: relative;
	text-align: left;
	@include xs {
		max-width: 270px;
		margin-left: auto;
		margin-right: auto; }
	&__buy-btn {
		position: absolute;
		width: calc(100% - 44px);
		left: 50%;
		transform: translateX(-50%);
		bottom: 18px;
		height: 50px;
		line-height: 50px;
		font-family: $rob;
		color: $white;
		font-size: 14px;
		text-transform: uppercase;
		background: linear-gradient(to right, #0082f9  0%, #00be9d  100%);
		border-radius: 5px;
		text-align: center; }
	&__labe {
		display: none; }
	&__img {
		height: 182px;
		img {
			object-fit: cover;
			width: 100%;
			height: 100%; } }
	&__text-block {
		padding: 22px 22px 70px 22px; }
	&__title {
		color: $black-f;
		font-family: $mont;
		font-size: 16px;
		font-weight: 700;
		line-height: 1;
		text-transform: uppercase;
		margin-bottom: 5px;
		display: block; }
	&__link {
		color: $green;
		font-family: $mont;
		font-size: 14px;
		font-weight: 700;
		line-height: 1;
		text-decoration: underline;
		display: inline-block;
		margin-bottom: 23px;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: -5px;
			left: -5px;
			right: -5px;
			bottom: -5px; }
		&:hover {
			text-decoration: none; }
		// &--more
		// 	display: inline-block
		// 	margin-top: 12px
		// 	font-size: 14px
		// 	font-weight: 700
		// 	line-height: 1
		// 	position: absolute
		// 	left: 22px
 }		// 	bottom: 18px
	&__char {
		color: $black-dark;
		font-size: 13px;
		font-weight: 700;
		line-height: 1;
		text-transform: uppercase;
		margin-bottom: 11px;
		span {
			font-size: 13px;
			font-weight: 400; } }
	&__bottom-text {
		color: $gray-sec;
		line-height: 1;
		font-size: 14px; }
	&--smaller {
		.mp-prod__text-block {
			padding: 22px 22px 22px 22px; }
		.mp-prod__title {
			margin-bottom: 23px; } }
	&--smallest {
		.mp-prod__text-block {
			padding: 22px 22px 12px 22px; }
		.mp-prod__title {
			margin-bottom: 15px; }
		text-align: center; } }
