.error-page {
	text-align: center;
	margin-bottom: 88px;
	margin-top: 65px;
	&__img {
		@include cover;
		margin: 0 auto 50px;
		@include m {
			width: 280px;
			height: 150px; }
		&-404 {
			width: 528px;
			height: 302px; }
		&-500 {
			width: 452px;
			height: 324px; } }
	&__title {
		margin-bottom: 34px; }
	&__stitle {
		font-size: 26px;
		font-weight: 500;
		color: $black;
		line-height: 1.4; }
	&__text {
		color: #7e7e7e;
		font-size: 20px;
		font-weight: 400;
		line-height: 1.8;
		margin-bottom: 57px;
		@include m {
			br {
				display: none; } } }
	&__btn {
		padding: 12px 50px; } }
