.prefooter {
	padding-bottom: 90px;
	@include m {
		padding-bottom: 40px; }
	&__wrapper {
		background-size: cover;
		background-repeat: no-repeat;
		padding: 115px 46px 112px 56px;
		//font-size: 0
		border-radius: 5px;
		position: relative;
		@include clr;
		@include r(1150) {
			padding: 115px 26px 112px 26px; }
		@include m {
			padding: 36px 26px 38px;
			text-align: center; } }
	&__left, &__center {
		//display: inline-block
		//vertical-align: top
		@include m {
			display: block; } }
	&__left {
		float: left;
		//margin-right: 5%
		@include m {
			float: none;
			margin-right: 0;
			margin-bottom: 20px; }
		strong {
			font-family: $mont;
			font-weight: 900;
			font-size: 26px;
			color: $white-sec;
			text-transform: uppercase;
			line-height: 1.46;
			@include m {
				font-size: 24px;
				br {
					display: none; } } } }
	&__center {
		color: $white;
		font-size: 16px;
		font-weight: 500;
		line-height: 1.75;
		text-transform: uppercase;
		max-width: 520px;
		padding: 4px 50px 9px;
		border-left: 1px  solid rgba(255, 255, 255, 0.15);
		border-right: 1px solid  rgba(255, 255, 255, 0.15);
		text-align: left;
		position: absolute;
		top: 50%;
		left: 52.5%;
		transform: translate(-50%, -50%);
		@include r(1150) {
			max-width: 480px;
			padding: 4px 20px 9px; }
		@include r(1100) {
			max-width: 430px; }
		@include m {
			padding: 26px 0;
			width: 100%;
			border-top: 1px  solid rgba(255, 255, 255, 0.15);
			border-bottom: 1px solid  rgba(255, 255, 255, 0.15);
			border-left: none;
			border-right: none;
			margin-bottom: 30px;
			position: relative;
			transform: none;
			max-width: 100%;
			top: 0;
			left: 0;
			text-align: center; } }
	&__btn {
		float: right;
		margin-top: 10px;
		margin-right: 10px;
		@include r(1150) {
			margin-right: 0; }
		@include m {
			float: none; } } }
