.popup {
	z-index: 2223;
	position: fixed;
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	font-size: 0;
	display: none;
	background: rgba(51, 51, 51, 0.8);
	text-align: center;
	padding-top: 5vh;
	overflow-y: scroll;
	@include m {
		padding: 30px 10px 30px; }
	&--big-padding {
		padding-top: 7vh; }
	.slick-prev-custom {
		left: -85px; }
	.slick-next-custom {
		right: -87px; }
	&__title {
		//text-align: center
		margin-bottom: 34px;
		display: block;
		@include dl {
			font-size: 32px;
			margin-bottom: 26px; }
		&--spec {
			color: $black;
			font-family: $mont;
			font-size: 18px;
			font-weight: 700;
			line-height: 1;
			text-transform: uppercase;
			text-align: center;
			overflow: hidden;
			margin-bottom: 28px;
			@include dl {
				font-size: 18px; }
			strong {
				position: relative;
				display: inline-block;
				//background: $white
				&:before, &:after {
					content: '';
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					background-color: #dde6ec;
					height: 1px;
					width: 300px; }
				&:before {
					left: 125%; }
				&:after {
					right: 125%; } } } }
	&__text {
		color: $black;
		font-size: 15px;
		line-height: 1.8;
		margin-bottom: 20px;
		@include dl {
			margin-bottom: 10px; } }
	&__wrapper {
		background: $white;
		border-radius: 10px;
		padding: 58px;
		width: 482px;
		font-size: 0;
		margin-left: auto;
		margin-right: auto;
		@include dl {
			padding: 17px;
			width: 350px; }
		@include m {
			//
			width: 100%; } }
	&__bottom {
		margin-top: 37px;
		font-size: 0;
		text-align: left;
		@include m {
			margin-top: 28px; } }
	&__bottom-left, &__bottom-right {
		width: 50%;
		display: inline-block;
		vertical-align: top;
		@include m {
			display: block;
			width: 100%;
			text-align: center; } }
	&__bottom-right {
		text-align: right; }
	&__link {
		font-size: 14px;
		text-decoration: underline;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: -5px;
			right: -5px;
			bottom: -5px;
			left: -5px; }
		@include tmin {
			&:hover {
				text-decoration: none; } } }
	&__btn-grad {
		padding: 15px 15px;
		display: block;
		margin-left: auto;
		margin-right: auto;
		max-width: 290px;
		text-align: center;
		position: relative;
		@include m {
			padding: 15px 15px 15px 40px; }
		svg {
			fill: $white;
			font-size: 17px;
			position: absolute;
			top: 50%;
			left: 25px;
			margin-top: -8px;
			@include m {
				left: 15px; } } }
	&__submit {
		border-radius: 29px; }
	&__slider-wrapper {
 }		//height: 650/1080*100 + vh
	&__close-btn {
		position: absolute;
		top: -30px;
		right: 0; }
	&__content {
		display: inline-block;
		position: relative;
		@include m {
			display: block;
			width: 280px;
			margin: 0 auto; }
		&--slider {
			width: percentage(980/1920);
			height: percentage(775/1080); } }
	.slide {
		//height: 88%
		//height: 88%
		//max-height: 650px
		height: 100%;
		max-height: 650px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; }
		@include ds {
			max-height: 450px; }
		//+r(1340)
 }		//	max-height: 400px
	.slide-tumb {
		cursor: pointer;
		width: 127px;
		margin: 0 15px;
		height: 84px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } } }
.p-gallery {
	//height: 70vh
	&__main-slider {

		margin-bottom: 2%; }
	&__tumb-slider {
 } }		//height: 10%
.pfield {
	margin-bottom: 18px;
	position: relative;
	@include m {
		width: 100%; }
	svg {
		fill: $placeholder-color;
		font-size: 25px;
		position: absolute;
		left: 25px;
		top: 50%;
		margin-top: -12px; }

	&--full {
		width: 100%; }
	&--half {
		display: inline-block;
		vertical-align: top;
		width: 47%;
		margin-right: 6%;
		&:nth-of-type(even) {
			margin-right: 0; }
		@include m {
			margin-right: 0;
			display: block; } }
	&--email {
		svg {
			left: 24px; } }
	&--check {
		padding-bottom: 0;
		label {
			text-align: left;
			@include nosel; }
		@include m {
			margin-bottom: -5px; } }
	&--pass {
		svg {

			left: 28px; } }
	&--firstname, &--lastname {
		svg {} }

	&--select {
		text-align: left;
		.select-view {
			background-color: $field-bg;
			//background: red
			height: 52px;
			font-size: 14px;
			padding: 4px 35px 11px 62px;
			line-height: 3;
			margin-bottom: 0;
			@include m {
				background-color: $field-bg-m; } }
		.select_wrapper {
			width: 100%; }
		svg {
			font-size: 15px;
			z-index: 2;
			margin-top: -8px; }
		ul.select-options li {

			&:hover {
				background: $green;
				color: $white; } } }
	&__input {
		background-color: $field-bg;
		//background: red
		font-size: 14px;
		line-height: 52px;
		appearance: none;
		letter-spacing: -0.5px;
		border: 1px solid $field-bg;
		height: 52px;
		padding: 0 22px 0 62px;
		border-radius: 27px;
		display: block;
		width: 100%;
		@include trans;
		@include m {
			background-color: $field-bg-m; } } }

.popup-login {
	&__submit {
		margin-top: 13px;
		margin-bottom: 40px;
		@include dl {
			margin-top: 5px;
			margin-bottom: 35px; }
		@include m {
			margin-top: 20px; } } }
.popup-sign {
	@include dl {
		padding-top: 2vh; }
	.popup__wrapper {
		width: 630px;
		padding: 30px;
		@include dl {
			padding: 17px; }
		@include m {
			width: 100%; } }
	.popup__bottom {
		border-top: 1px solid #dde6ec;
		margin-top: 32px;
		padding-top: 35px;
		@include dl {
			margin-top: 20px;
			padding-top: 20px; } }
	.popup__bottom-left {
		width: 59.5%; }
	.popup__bottom-right {
		width: 40.5%; }
	.popup__bottom-left, .popup__bottom-right {
		@include m {
			width: 100%; } }
	.field__radio-inner {
		width: 100%; }
	.field__input-radio-icon {
		top: 20%; }
	.popup__title {
		margin-top: 18px;
		margin-bottom: 30px;
		@include dl {
			margin-top: 5px;
			margin-bottom: 18px; }
		@include m {
			margin-top: 17px; } } }

.pclose-btn {
	width: 22px;
	height: 22px;
	font-size: 15px;
	color: $white;
	@include s(popup-close-btn);
	opacity: 0.6;
	cursor: pointer;
	@include trans;
	&:hover {
		opacity: 1; }
	&:before {
		content: '';
		position: absolute;
		top: -10px;
		right: -10px;
		left: -10px;
		bottom: -10px; } }
.psel-method {
	.popup__wrapper {
		padding: 0;
		@include dl {
			width: auto; } }
	&__top {
		padding: 15px 28px;
		position: relative;
		border-bottom: 1px solid  #dde6ec; }
	p {
		color: $black-sec;
		font-family: $mont;
		font-size: 20px;
		font-weight: 900;
		line-height: 1;
		text-transform: uppercase;
		text-align: left; }
	.popup__close-btn {
		@include s(select-close-popup);
		top: 16px;
		right: 28px; }
	&__cont {
		font-size: 18px;
		font-weight: 400;
		line-height: 1.77;
		color: $black;
		padding: 50px 28px 70px;
		a {
			text-decoration: underline;
			position: relative;
			&:hover {
				text-decoration: none; }
			&:before {
				content: '';
				position: absolute;
				top: -8px;
				right: -8px;
				bottom: -8px;
				left: -8px; } } } }

