.dashboard {
	&__left, &__right {
		width: 48.5%;
		display: inline-block;
		@include dt {
			display: block;
			width: 100%;
			margin-right: 0; }
		svg {
			font-size: 30px;
			@include dl {
				font-size: 26px; }
			@include nb {
				font-size: 22px; }
			@include dt {
				font-size: 30px; } } }
	&__left {
		//padding-right: 25px
		margin-right: 3%;
		@include dl {
			margin-bottom: 20px; }
		svg {
			fill: $green; } }
	&__right {
		//padding-left: 25px
		svg {
			fill: $blue; } }
	&__bottom {
		border-top: 1px solid #dde6ec;
		padding-top: 35px;
		margin-top: 32px;
		text-align: center; }
	.title-thin {
		margin-bottom: 23px;
		text-align: center; }
	&__dashblock {
		margin-right: 5%;
		&:nth-of-type(even) {
			margin-right: 0; } }
	&__create-btn {
		margin-right: 49px;
		@include m {
			margin-bottom: 20px; } }
	//&__wrapper
	//	+dr
	//		padding: 0 15px
	.title-adm {
		@include dr {
			margin: 0 15px; } } }
.dashblock {
	//padding: 33px 1% 5px 8
	padding: 33px 0 5px;
	display: inline-block;
	vertical-align: top;
	width: 47.5%;
	min-height: 145px;
	background: $white;
	border-radius: 4px;
	box-shadow: 0 6px 12px rgba(189, 197, 201, 0.2);
	font-size: 0;
	margin-bottom: 20px;
	@include ds {
		//padding: 33px 1% 5px 6%

		min-height: 130px; }
	@include dl {
 }		//padding: 33px 1% 5px 6%
	@include nb {
		//padding: 40px 1% 5px 4%
		padding: 40px 0 5px; }
	@include dt {
		text-align: center;
		padding: 20px 10px;
		min-height: 106px; }
	@include xs {
		display: block;
		margin-right: 0;
		width: 100%; }
	&__img {
		display: inline-block;
		//width: 60px
		width: 82px;
		padding-top: 5px;
		vertical-align: top;
		text-align: right;
		@include dl {
			width: 50px; }
		@include nb {
			padding-top: 0;
			width: 50px; }
		@include dt {
			padding-top: 5px;
			margin-right: 10px;
			text-align: left; } }
	&__right {
		display: inline-block;
		vertical-align: top;
		width: calc(100% - 82px);
		padding-left: 20px;
		@include nb {
			//padding-left: 10px
			width: calc(100% - 50px); }
		@include dt {
			text-align: left;
			width: auto;
			padding-left: 0; } }


	&__main-text {
		font-size: 38px;
		color: $black-sec;
		line-height: 1;
		margin-bottom: 10px;
		display: inline-block;
		vertical-align: top;
		font-size: 38px;
		@include ds {
			font-size: 30px; }
		@include nb {
			font-size: 20px; }
		@include dt {
			font-size: 38px; } }
	&__sec-text {
		font-size: 18px;
		font-family: $mont;
		color: $gray-sec;
		line-height: 1;
		//padding-left: 60px
		font-size: 18px;
		@include ds {
			font-size: 16px; }
		@include dl {
 }			//padding-left: 50px
		@include nb {
			font-size: 14px; }
		@include dt {
			font-size: 18px; } }
	&__dots {
		margin-bottom: 15px;
		span {
			display: inline-block;
			width: 9px;
			height: 9px;
			background: $green;
			margin-right: 5px;
			border-radius: 50%;
			&:last-of-type {
				margin-right: 0; } }
		&--blue {
			span {
				background: $blue; } } }
	&--more {
		padding: 52px 33px 0!important;
		text-align: center;
		margin-right: 0;
		@include dt {
			padding: 38px 33px 0!important; } } }
