.adm-spacel {
	&__inner {
		position: relative; } }

.edit-ad-space {
	position: absolute;
	top: 30px;
	right: 30px;
	@include dt {
		position: relative;
		top: 0;
		right: 0;
		margin-bottom: 20px;
		text-align: right; }
	&-btn {
		font-family: $rob;
		font-size: 12px;
		border-radius: 31px;
		padding: 14px 23px;
		font-weight: bold;
		color: #00be9d;
		background: #ecf2f6;
		transition: all 0.3s;
		text-transform: uppercase;
		.icon-pencil {
			fill: #00be9d;
			transition: all 0.3s; }
		span {
			margin-left: 11px; }
		&:hover {
			color: #fff;
			background: #00be9d;
			.icon-pencil {
				fill: #fff; } } } }
