.field {
	&--select {
		&-medium {
			width: 220px;
			@include r(991) {
				width: auto; }
			.select-options {
				max-height: 200px;
				overflow-y: scroll; } }
		&-small {
			min-width: 130px;
			text-align: center;
			.select-options {
				max-height: 200px;
				overflow-y: scroll; }
			.select_wrapper {
				width: 100%; }
			@include m {
				text-align: left; } } }
	&_medium {
		width: 48%;
		margin-right: 4%;
		@include r(991) {
			width: auto; }
		&:last-of-type {
			margin-right: 0; }
		input {
			width: 100%; } }
	&_mediums {
		width: 55%;
		@include r(991) {
			width: 100%; }
		input {
			width: 100%; } } }

.fields {
	&_first {
		padding-top: 0; }
	&_b {
		padding-bottom: 20px; } }
