.post {
	text-align: left;
	&__inner {
		position: relative;
		margin-bottom: 18px;
		@include m {
			margin-bottom: 14px; } }
	&__img {
		position: relative;
		height: 282px;
		&:before {
			content: '';
			@include coverdiv;
			opacity: 0.75;
			background-image: linear-gradient(to top, #242d36 0%, rgba(36, 45, 54, 0.55) 100%);
			border-radius: 5px; }
		@include r(499) {
			height: 216px; }
		img {
			object-fit: cover;
			width: 100%;
			height: 100%;
			border-radius: 5px; } }
	&__title, 	&__date {
		font-family: $mont;
		position: absolute;
		left: 0;
		padding: 0 26px;
		color: $white;
		@include m {
			padding: 0 22px; } }
	&__title {
		font-size: 18px;
		font-weight: 700;
		text-transform: uppercase;
		line-height: 1.22;
		bottom: 43px;
		@include m {
			bottom: 36px; } }
	&__date {
		font-size: 14px;
		line-height: 1.57;
		bottom: 20px;
		@include m {
			bottom: 13px; } }
	&__text {
		margin-bottom: 10px; }
	&--small {
		padding-bottom: 19px;
		margin-bottom: 28px;
		border-bottom: 1px solid $border-gray;
		@include m {
			padding-bottom: 14px;
			margin-bottom: 19px; }
		.post__title, .post__date {
			position: relative;
			left: 0;
			bottom: 0;
			padding: 0; }
		.post__title {
			color: $black-dark;
			font-size: 15px;
			margin-bottom: 2px;
			@include m {
				font-size: 17px;
				margin-bottom: 10px; } }
		.post__text {
			font-size: 13px;
			margin-bottom: 2px; }
		.post__date {
			color: $gray-sec;
			font-size: 13px;
			margin-bottom: 12px; }
		.more-link {
			font-size: 14px; } } }
