.profile-manage {
	@include r(1099) {
		margin-left: 15px;
		margin-right: 15px; }
	@include m {
		margin-left: 0;
		margin-right: 0; }
	.soc {
		position: absolute;
		top: 7px;
		left: -2px;
		padding: 0;
		width: auto;
		background: inherit;
		border-right: none; }

	.upload {
		&__item {
			&--profile-pic {
				margin-bottom: 20px; } }

		&__profile-picture {
			z-index: 1;
			position: absolute;
			top: -22px;
			right: -22px;
			width: 42px;
			height: 42px;
			border-radius: 50%;
			background: #ecf2f6;
			border: 5px solid #fff; }
		.icon-pencil {
			width: 16px;
			height: 16px;
			fill: $green;
			@include vcenter; } }

	.fields {
		&--first {
			padding-top: 0; } }

	.field {
		&__image {
			margin-bottom: 10px;
			width: 176px;
			height: 118px;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 5px; } }
		&__input {
			&--lg {
				width: 495px;
				@include m {
					width: 100%; } } } } }
