.profile {
	&__right {
		font-size: 0;
		padding: 33px 29px 0;
		@include m {
			padding:  15px 0px; } }
	&__wrapper {

		&--border {
			border-bottom: 2px dashed $border-sec;
			margin-bottom: 28px;
			padding-bottom: 21px; } }
	&__main-title {
		span {
			&:before {
				left: 109%; }
			&:after {
				right: 109%; } } }
	&__block {
		&--half {
			display: inline-block;
			vertical-align: top;
			@include m {
				display: block;
				width: 100%; } }
		&--left {
			width: 284px; }
		&--right {
			width: 250px; }
		&--mb {
			margin-bottom: 29px; }
		&--spec {
			margin-top: 29px; }
		&--inner {
			margin-top: 29px; } }
	&__icon {
		width: 30px;
		display: inline-block;
		vertical-align: top;
		text-align: center;
		svg {
			fill: #c0cad1;
			font-size: 17px; }
		&--spec {
			svg {
				font-size: 20px; } } }
	&__title {
		color: $gray-sec;
		font-size: 13px;
		font-weight: 500;
		line-height: 1;
		text-transform: uppercase;
		display: inline-block;
		vertical-align: top;
		margin-bottom: 8px;
		&--big-mb {
			margin-bottom: 14px; }
		&--sm-letter {
			text-transform: none; } }
	&__link {
		color: $green;
		font-size: 15px;
		font-weight: 400;
		line-height: 1;
		position: relative;
		display: block;
		padding-left: 30px;
		&:before {
			content: '';
			position: absolute;
			top: -8px;
			right: -8px;
			left: -8px;
			bottom: -8px; }
		&--border {
			text-decoration: underline;
			&:hover {
				text-decoration: none; } } }
	&__text {
		color: #333;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.57;
		&--padding {
			padding-left: 30px; }
		&--mb {
			margin-bottom: 26px;
			padding-left: 30px; }
		@include m {
			padding-left: 30px; } }
	&__soc-block {
		border-top: 1px solid #e5ecf2;
		margin-top: 30px;
		margin-right: -29px;
		margin-left: -29px;
		@include m {
			margin: 10px 0;
			border-top: 0; } }
	&__after-text {
		color: $gray-sec;
		font-size: 13px;
		font-weight: 500;
		line-height: 2.15;
		text-transform: uppercase;
		margin: 20px 0;
		@include m {
			text-align: center; }
		svg {
			position: relative;
			top: 2px; } }
	.spacel__slider-wrapper {
		margin-bottom: 0;
		@include m {
			width: 280px;
			margin:  0 auto; } }
	.spacel__slide {
		height: 340px;
		@include m {
			height: 280px; } }
	&__tumb-slider {
		max-width: 600px;
		margin: 0 auto; }
	&__tumb-slide {
		width: 116px;
		height: 92px;
		border-radius: 5px;
		overflow: hidden;
		margin: 0 3px;
		cursor: pointer;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__list {
		font-size: 0; }
	&__list-title {
		margin-top: 27px;
		margin-bottom: 14px;
		text-align: center;
		&--sm-letter {
			text-transform: none; } }
	&__more-block {
		text-align: center;
		padding-bottom: 37px;
		margin-top: 14px;
		&--border {
			border-bottom: 1px solid #dde6ec; } }
	&__container {
		margin-bottom: 24px; }
	&__list--wrapper {
		display: flex;
		flex-wrap: wrap;
		flex-basis:  94/4 + %;
		@include m {
			display: block; } }
	&__table {
		display: table;
		margin-left: 30px; }
	&__table-row {
		display: table-row; }
	&__cell-char, &__cell-value {
		display: table-cell;
		padding-right: 8px;
		max-width: 50%;
		//width: 50%
		color: #333;
		font-size: 14px;
		line-height: 1.57;
		font-weight: 400; }
	&__cell-char {
		font-weight: 500; } }
.profile-pers {
	&__photo {
		height: 375px;
		border-radius: 5px;
		overflow: hidden;
		@include m {
			width: 280px;
			height: 280px;
			margin: 0 auto 10px; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } } }
.profile-medium, .profile-pers {
	.profile__main-title {
		span {
			&:before {
				left: 145%; }
			&:after {
				right: 145%; } } } }
.profile-medium {
	.profile__right {
		padding-bottom: 33px; } }
.adm-cont {
	.pblock__img {
		@include dt {
			height: 130px; } }
	.profile__block--half {
		@include dt {
			display: block; } } }
