.c-page {
	&__upper-title {
		margin: 25px 0;
		p {
			max-width: 600px;
			font-size: 15px;
			margin: 0 auto;
			color: #333;
			text-align: center;
			text-transform: uppercase; } }
	&__contact {
		font-size: 0;
		padding-bottom: 93px;
		margin-bottom: 79px;
		border-bottom: 1px solid #f5f5f5;
		@include m {
			padding-bottom: 20px;
			margin-bottom: 10px; } }
	&__contact-left {
		display: inline-block;
		width: 25%;
		vertical-align: top; }

	&__contact-right {
		display: inline-block;
		width: 63%;
		margin-left: 12%;
		box-shadow: 0 6px 22px rgba(123, 139, 149, .8);
		vertical-align: top; }
	&__contact-left, &__contact-right {
		@include m {
			display: block;
			width: 100%;
			text-align: center;
			margin-left: 0; } }

	&__form {
		font-size: 0;
		margin-bottom: 60px;
		.fields {
			border-bottom: none; } }
	&__btn {
		margin-top: 30px;
		text-align: center; }
	.container {
		@include m {
			padding: 0; } } }

.c-form {

	&__p {
		padding: 8px 0; } }

.a-contacts {

	&__ul {}

	&__item {
		position: relative;
		display: block;
		font-size: 18px;
		padding: 0 0 15px 52px;
		margin-bottom: 20px;
		border-bottom: 1px dashed #c9c9c9;
		font-weight: bold;
		color: #181818;
		text-transform: uppercase;
		&:last-of-type {
			border-bottom: none; }
		@include m {
			padding: 0;
			border-bottom: none; } }
	&__icon {
		position: absolute;
		left: 0;
		top: 8px;
		@include m {
			display: none; } }

	&__phone {
		display: block;
		font-size: 16px;
		color: #585858;
		font-weight: normal; }

	&__email {
		display: block;
		font-size: 16px;
		font-weight: normal;
		text-decoration: underline;
		text-transform: initial; }

	&__text {
		display: block;
		color: #585858;
		font-weight: normal;
		text-transform: initial; }
	&__phone,	&__email,	&__text {
		@include m {
			margin-top: 20px; } } }

.c-icon-phone {}


.icon-phonec {
	font-size: 27px;
	stroke: #000; }

.c-icon-email {
	top: 0;
	left: 0; }

.icon-emailc {
	font-size: 31px; }

.c-icon-address {
	top: 0;
	left: 0; }

.icon-addresses {
	font-size: 30px; }

#с-map {
	height: 280px; }


