.faq-page {
	&__content {
		margin-bottom: 88px;
		@include m {
			padding-top: 9px;
			margin-bottom: 40px; } }
	&__title-i {
		margin-bottom: 20px;
		@include m {
			margin-top: 12px;
			margin-bottom: 24px; } } }
.dropblock {
	padding-bottom: 38px;
	border-bottom: 1px solid $border-gray;
	margin-bottom: 30px;
	@include m {
		margin-bottom: 20px;
		padding-bottom: 20px; }
	&:last-of-type {
		border-bottom: none;
		margin-bottom: 0;
		padding-bottom: 0; }
	&__arrow {
		position: absolute;
		right: 13px;
		top: 8px;
		@include s(drop-arrow);
		@include trans; }
	&__title {
		margin-bottom: 20px;
		cursor: pointer;
		position: relative;
		color: $black;
		transition: color .3s;
		padding-right: 35px;
		@include m {
			margin-bottom: 0; } }
	&.is-open {
		.dropblock__arrow {
			transform: rotateX(180deg); }
		.dropblock__title {
			color: $black-dark; } }
	&__text {
		padding: 18px 23px;
		background-color: white;
		border-radius: 5px;
		box-shadow: 0 3px 12px rgba(0, 0, 0, 0.15);
		display: none;
		strong {
			font-weight: 700; }
		@include m {
			margin-top: 20px; } } }
.tabs {
	&__nav {
		font-size: 0;
		text-align: center;
		border-bottom: 2px solid  $border-gray;
		@include m {
			display: none;
			position: absolute;
			left: 0;
			top: 100%;
			width: 100%;
			z-index: 1;
			background-color: $white;
			border-bottom: none; }
		a {
			font-size: 14px;
			text-transform: uppercase;
			line-height: 3.14;
			display: block;
			position: relative;
			&:before, &:after {
				content: '';
				position: absolute; }
			&:after {
					width: 1px;
					height: 19px;
					right: 0;
					top: 50%;
					transform: translateY(-50%);
					background: #f3f3f3;
					@include m {
						display: none; } }
			@include tmin {
				&:before {
					@include trans;
					bottom: -2px;
					left: 0;
					width: 100%;
					height: 3px;
					background: transparent; }
				&:hover {
					&:before {
						background: $green; } } } } }
	&__nav-item {
		display: inline-block;
		width: 33%;
		&:last-of-type {
			a {
				&:after {
					display: none; } } }
		@include m {
			display: block;
			width: 100%; } }
	&__nav-wrapper {
			margin-bottom: 84px;
			position: relative;
			@include m {
				margin-left: auto;
				margin-right: auto;
				width: 280px;
				margin-bottom: 32px; } }
	&__view {
		display: none;
		//height: 54px
		padding: 20px 40px 20px 54px;
		line-height: 1;
		background-color: #f3f4f4;
		border-radius: 27px;
		position: relative;
		font-size: 16px;
		color: $black-sec;
		&:before, &:after {
			content: '';
			position: absolute;
			top: 50%;
			//margin-top: -7px
			transform: translateY(-50%); }
		&:before {
			@include s(drop-icon);
			left: 22px; }
		&:after {
			@include s(drop-arrow);
			@include trans;
			right: 25px;
 }			//margin-top: -3px
		&.is-open {
			&:after {
				transform: rotateX(180deg); } }
		@include m {
			display: block; } } }

