.adst-section {
	display: none;
	padding: 36px 0 0 0;
	&-cr {
		&.is-active {
			padding-top: 0; } }
	&.is-active {
		display: block; }
	&.tab-item-empty {
		.create-market {
			position: relative;
			top: 0;
			right: 0;
			margin-top: 10px; } } }
.adst-tab {
	font-size: 0;

	&__more {
		text-align: center; } }

.adstnav {

	&__tablinks {
		position: relative;
		&:after {
			content: '';
			position: absolute;
			left: 0;
			width: 100%;
			height: 2px;
			bottom: -13px;
			background: #dde6ec; }
		@include r(1400) {
			display: flex;
			justify-content: space-between; }
		@include m {
			display: block; } }
	&__link {
		font-family: $rob;
		position: relative;
		display: inline-block;
		width: 20%;
		font-size: 14px;
		color: #181818;
		text-align: center;
		text-transform: uppercase;
		cursor: pointer;
		border-right: 1px solid #dde6ec;
		@include r(1150) {
			border-right: none; }
		@include m {
			display: block;
			width: 100%;
			margin-bottom: 10px; }
		// &:after
		// 	content: ''
		// 	position: absolute
		// 	right: 0
		// 	top: 0
		// 	width: 1px
		// 	height: 100%
		// 	background: #dde6ec
		@include r(1500) {
			padding: 0 40px;
			&:first-of-type {
				padding-left: 0; } }
		@include r(1400) {
			width: 	auto; }
		@include r(1250) {
			padding: 0 42px 0 0;
			// &:after
 }			// 		right: -42px
		@include r(1150) {
			padding: 0 22px 0 0;
			&:last-of-type {
				padding-right: 30px; } }
		@include m {
			padding: 0;
			&:last-of-type {
				padding-right: 0;

				// &:after
 } }				// 	right: 0
		span {
			position: relative;

			&:after {
				content: '';
				position: absolute;
				bottom: -17px;
				width: 0;
				height: 3px;
				left: 0;
				background: #00be9d;
				transform: translateX(0%);
				transition: all 0.3s;
				z-index: 1;
				@include m {
					display: none; } } }

		&.is-active {
			color: #00be9d;
			span {
				&:after {
					width: 100%; } } } }


	&__message {
		position: absolute;
		right: -33px;
		top: 50%;
		transform: translateY(-50%);
		width: 24px;
		height: 24px;
		font-size: 10px;
		font-style: inherit;
		color: #fff;
		border-radius: 50%;
		background: #0082f9;
		line-height: 24px;
		text-align: center; } }

.adst-app {
	&__content {
		margin-bottom: 10px;
		display: flex;
		flex-wrap: wrap;
		@include m {
			display: block; } } }

.adst-inner {
	//display: inline-block
	width: 49%;
	background: #fff;
	margin-bottom: 25px;
	padding: 23px 23px 18px 23px;
	border-radius: 7px;
	box-shadow: 0 0 18px rgba(0,0,0,.1);
	&:nth-of-type(odd) {
		margin-right: 2%; }
	@include m {
		width: 280px;
		margin: 0 auto 20px;
		&:nth-of-type(odd) {
			margin-right: auto; } }
	&_r {
		padding: 22px 5px 18px 15px;
		@include m {
			padding:  15px; }
		// +r(1580)
		// 	width: 46%
		// 	&:nth-of-type(odd)
 }		// 		margin-right: 8%
	@include r(1450) {
		padding: 20px 10px 18px 10px; }
	&__img {
		display: inline-block;
		width: 40%;
		height: 195px;
		text-align: center;
		vertical-align: top;
		border-top-left-radius: 5px;
		border-bottom-left-radius: 5px;
		border-top-right-radius: 5px;
		border-bottom-right-radius: 5px;
		overflow: hidden;
		@include r(1150) {
			display: block;
			width: 200px;
			margin: 0 auto 20px; }
		// +r(1500)
		// 	width: 100%
		// 	margin-bottom: 15px
		// +r(1380)
		// 	width: 100%
		// 	margin-bottom: 15px
		img {
			width: auto;
			height: 100%; } }
	&__img_p {
		height: 245px;
		// +r(1580)
		// 	width: 100%
 }		// 	margin-bottom: 15px
	&__text {
		display: inline-block;
		width: 60%;
		padding: 0 0 0 25px;
		vertical-align: top;
		line-height: 1;
		// +r(1500)
		// 	width: 100%
		// 	padding: 0
		@include r(1300) {
			padding-left: 12px; }
		@include r(1150) {
			width: 280px;
			margin: 0 auto;
			display: block; }
		&_s {
			padding: 0 0 0 22px;
			@include r(1580) {
				padding-left: 12px; } } }
	&__title {
		margin-bottom: 7px;
		p {
			font-family: $mont;
			font-size: 16px;
			font-weight: bold;
			color: #333;
			text-transform: uppercase;
			@include r(1400) {
				font-size: 15px; } } }
	&__item {
		margin-bottom: 25px;
		a {
			font-family: $mont;
			font-size: 14px;
			font-weight: bold;
			text-decoration: underline;
			@include r(1400) {
				font-size: 13px; } }
		.a-blue {
			position: relative;
			color: #0082f9;
			margin-right: 17px;
			&:after {
				content: '/';
				position: absolute;
				right: -11px;
				color: #000; } }
		.a-green {
			color: #00be9d; } }
	&__upper {
		padding: 15px 0;
		margin-bottom: 18px;
		border-top: 1px solid #dde6ec;
		border-bottom: 1px solid #dde6ec;
		// +r(1500)
 }		// 	text-align: center
	&__p-upper {
		margin-bottom: 6px;
		padding-bottom: 6px;
		border-bottom: 1px solid #dde6ec;
		@include r(1580) {
			text-align: left; } }

	&__left, &__p-left, &__left-p {
		display: inline-block;
		// max-width: 50%
		margin-right: 25px;
		vertical-align: top;
		line-height: 1.7;
		text-align: left;
		@include r(1500) {
			margin-right: 10px; }

		p {
			font-family: $rob;
			font-size: 13px;
			font-weight: bold;
			color: #181818;
			text-transform: uppercase;
			@include r(1400) {
				font-size: 12px; }
			span {
				margin-left: 10px;
				font-weight: normal; } } }
	&__p-left {
		margin-right: 63px;
		@include r(1100) {
			max-width: 65%;
			margin-right: 30px; } }
	&__right, &__p-right, &__right-p {
		display: inline-block;
		max-width: 50%;
		vertical-align: top;
		line-height: 1.7;
		text-align: left;
		@include r(1250) {
			max-width: 40%;
			text-align: left; }
		p {
			font-family: $rob;
			font-size: 13px;
			color: #181818;
			@include r(1400) {
				font-size: 12px; } } }
	&__lower {
		// +r(1500)
 }		// 	text-align: center
	&__p-lower {
		@include r(1580) {
			text-align: left; } }
	&__passed {
		position: relative;
		// padding-left: 27px
		line-height: 1.7;
		p {
			position: relative;
			display: inline-block;
			font-family: $rob;
			font-size: 14px;
			color: #585858;
			text-transform: uppercase;
			@include r(1400) {
				font-size: 13px; }
			&:before {
				content: '';
				position: absolute;
				@include s(time-ads);
				top: 4px;
				left: -26px; } }
		span {
			font-family: $rob;
			font-size: 13px;
			font-weight: bold;
			color: #181818;
			padding-right: 58px;
			text-transform: uppercase;
			@include r(1500) {
				padding-right: 42px; }
			@include r(1400) {
				font-size: 12px; } } }
	&__p-passed {
		position: relative;
		line-height: 1.7;
		// +r(1600)
		// 	padding-right: 109px
		// +r(1200)
		// 	padding-right: 0
		p {
			font-family: $rob;
			font-size: 13px;
			font-weight: bold;
			color: #181818;
			text-transform: uppercase; }
		span {
			font-family: $rob;
			font-size: 13px;
			color: #181818;
			font-weight: normal; } }
	&__left-p {
		max-width: none;
		margin-right: 3px;
		p {} }
	&__right-p {
		max-width: 80%;
		@include r(1580) {
			max-width: 70%; }
		@include r(1300) {
			max-width: 63%; } } }

.adst-more-btn {
	font-family: $rob;
	display: inline-block;
	padding: 20px 78px;
	font-size: 14px;
	border: 1px solid #00be9d;
	color: #00be9d;
	background: inherit;
	font-weight: 500;
	border-top-left-radius: 31px;
	border-bottom-left-radius: 31px;
	border-top-right-radius: 31px;
	border-bottom-right-radius: 31px;
	transition: all 0.3s;
	text-transform: uppercase;
	&:hover {
		background: #00be9d;
		color: #fff; } }


.my-adst {
	//text-align: center
	&__item {
		display: inline-block;
		width: 19%;
		margin-right: 1.2%;
		margin-bottom: 25px;
		vertical-align: top;
		text-align: center;
		&:nth-of-type(5n) {
			margin-right: 0; }
		@include r(1400) {
			width: 23%;
			margin-right: 2.5%;
			&:nth-of-type(4n) {
				margin-right: 0; }
			&:nth-of-type(5n) {
				margin-right: 2.5%; } }
		@include m {
			display: block;
			margin: 0 auto 20px;
			width: 280px;
			&:nth-of-type(4n),
			&:nth-of-type(5n) {
				margin-right: auto; } } }
	&__link {}

	&__img {
		height: 182px;
		max-width: 225px;
		margin: 0 auto;
		overflow: hidden;
		border-radius: 5px;
		@include r(1150) {
			height: 150px; }
		@include m {
			width: 100%;
			margin: 0;
			max-width: 280px;
			height: 180px; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }

	&__text {
		display: flex;
		align-items: center;
		justify-content: center;
		max-width: 225px;
		margin: 0 auto;
		min-height: 66px;
		background: #fff;
		box-shadow: 0 0 18px rgba(0, 0, 0, 0.2);
		@include m {
			max-width: 280px; }
		p {
			font-family: $mont;
			font-size: 15px;
			color: #555;
			font-weight: bold;
			padding: 0 18px;
			text-transform: uppercase;
			line-height: 1.4; } } }

.adst-create {
	padding: 23px 28px;
	margin-bottom: 37px;
	border-radius: 5px;
	background-color: #fff;
	box-shadow: 0 6px 22px rgba(123, 139, 149, 0.3);
	@include m {
		padding: 0;
		background: none;
		box-shadow: none; }

	.fields {
		padding-bottom: 17px;
		&_ads {
			padding-bottom: 26px;
			@include m {
				padding-bottom: 15px; } } }
	&__select {
		padding-top: 19px;
		.select_wrapper {
			width: auto; }
		.field--select-big {
			width: 48%;
			margin-right: 4%;
			&:nth-of-type(2n) {
				margin-right: 0; }
			@include r(991) {
				width: 100%; } } } }

.btn--create-adst {
	padding: 22px 72px;
	font-size: 14px;
	border-radius: 30px; }

.title-adst-cr {
	span {
		position: relative;
		display: inline-block;
		//background: #f2f7fb
		&:before, &:after {
			content: '';
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			background-color: #dde6ec;
			height: 1px;
			width: 1500px; }
		&:before {
			left: 125%; }
		&:after {
			right: 125%; } } }

.adst-target {
	margin-bottom: 10px;
	text-align: center;
	&__item {
		display: inline-block;
		width: 18.5%;
		margin-right: 1.875%;
		margin-bottom: 25px;
		vertical-align: top;
		background: #fff;
		box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
		border-radius: 7px;
		min-height: 198px;
		padding: 17px 17px 0 17px;
		line-height: 1.5;
		&:nth-of-type(5n) {
			margin-right: 0; }
		@include r(1500) {
			width: 23%;
			margin-right: 2.6%;
			&:nth-of-type(4n) {
				margin-right: 0; }
			&:nth-of-type(5n) {
				margin-right: 2.6%; } }
		@include r(1280) {
			width: 32%;
			margin-right: 2%;
			&:nth-of-type(4n) {
				margin-right: 2%; }
			&:nth-of-type(5n) {
				margin-right: 2%; }
			&:nth-of-type(3n) {
				margin-right: 0; } }
		@include m {
			display: block;
			width: 280px;
			margin:  0 auto 20px;
			&:nth-of-type(3n),
			&:nth-of-type(4n),
			&:nth-of-type(5n) {
				margin-right:  auto; } } }
	&__title {
		min-height: 57px;
		padding-bottom: 14px;
		margin-bottom: 19px;
		border-bottom: 1px solid #dde6ec;
		// +r(1500)
		// 	min-height: 80px
		p {
			font-family: $mont;
			font-size: 14px;
			color: #555;
			font-weight: bold;
			text-transform: uppercase; } }

	&__btn {
		margin-bottom: 10px; } }
.edit-target-btn {
	font-family: $rob;
	width: 100%;
	font-size: 12px;
	padding: 10px 0;
	margin-bottom: 8px;
	.icon-pencil {
		transition: all 0.3s;
		fill: #00be9d; }
	&:hover {
		.icon-pencil {
			fill: #fff; } }
	span {
		margin-left: 11px; } }

.search-target-btn {
	font-family: $rob;
	width: 100%;
	font-size: 12px;
	padding: 9px 8px 5px 5px;
	color: #0b87f9;
	border: 1px solid #0b87f9;
	.icon-search-icon {
		transition: all 0.3s;
		width: 18px;
		height: 18px;
		fill: #0b87f9; }
	&:hover {
		background: #0b87f9;
		.icon-search-icon {
			fill: #fff; } }
	span {
		vertical-align: super; } }

.create-market {
	position: absolute;
	top: 27px;
	right: 65px;
	@include r(1300) {
		right: 45px; }
	@include r(1250) {
		right: 25px; }
	&-visible {
		display: block; }
	@include m {
		position: relative;
		top: 0;
		right: 0;
		margin-bottom: 20px;
		text-align: center; }
	&-btn {
		font-family: $rob;
		font-size: 13px;
		color: #0b87f9;
		border-radius: 31px;
		border: 1px solid #0b87f9;
		padding: 12px 18px;
		background: inherit;
		transition: all 0.3s;
		text-transform: uppercase;

		.icon-createMarket {
			fill: #0b87f9;
			margin-right: 7px;
			transition: all 0.3s; }
		span {
			vertical-align: top; }
		&:hover {
			background: #0b87f9;
			color: #fff;
			.icon-createMarket {
				fill: #fff; } } } }



