.audience {
	@include r(1099) {
		margin-left: 15px;
		margin-right: 15px; }
	@include m {
		margin-left: 0;
		margin-right: 0; }
	&__textual {
		display: inline-block;
		color: #333333;
		line-height: 46px;
		margin: 0 12px;
		font-size: 14px;
		font-weight: 400; }

	&__title {
		color: #333333;
		font-size: 15px;
		font-weight: 700;
		line-height: 25px;
		text-transform: uppercase; }

	.fields {
		padding-top: 15px;
		padding-bottom: 10px; }

	.field {
		margin-right: 0;
		&--check {
			padding-bottom: 0; }
		&__title {
			margin-bottom: 0;
			&--location {
				margin: 6px 0 16px 0; } }

		&__input {
			padding: 0 22px;
			height: 46px;
			font-size: 14px;
			&--xs {
				width: 122px;
				padding-right: 35px; }
			&--sl {
				width: 212px; }
			&--sm {
				width: 170px; }
			&--s {
				width: 270px; }
			&--ml {
				width: 296px; }
			&--m {
				width: 352px; }
			&--md {
				width: 380px; }
			&--lg {
				width: 495px; }
			&--number {
				padding-right: 22px;
				@include placeholder {
					text-align: right; } }
			@include m {
				width: 100%; } }

		&--multiple {
			float: right;
			@include r(1099) {
				float: none; } }

		&--multiple-line {
			position: relative;
			&:before {
				position: absolute;
				top: 14px;
				content: "";
				height: 22px;
				width: 1px;
				background-color: #d4dce1; } }

		&--check {
			display: block;
			margin-right: 0; }

		&__radio-wrapper {
			max-width: 100%;
			width: 100%; }

		&__radio-inner {
			width: 25%;
			@include m {
				width: 100%;
				margin: 0 auto 10px; } }

		&__wrapper {
			&:after {
				position: absolute;
				right: 22px;
				top: 13px;
				content: "";
				height: 15px;
				color: #333;
				font-size: 14px;
				font-weight: 400; } }

		&__persent {
			&:after {
				content: "%"; } }

		&__price {
			&:after {
				content: "$";
				left: 13px;
				width: 12px; } } }

	.select_wrapper {
		width: 270px; }

	.select-view {
		padding: 15px 35px 15px 22px; }

	.specific {
		display: inline-block;
		.field__radio-inner {
			width: 100%;
			label {
				display: inline-block;
				text-transform: uppercase;
				font-weight: 500;
				font-size: 13px; } } }

	.specific-location {
		.field__radio-inner {
			margin-top: 10px; } }

	.specific-title {
		.field {
			&__radio-inner {
				margin-top: 45px; } } }
	.specific-title-data {
		float: right;
		@include m {
			float: none; }
		.field {
			&__title {
				margin-bottom: 5px; }
			&__wrapper {
				margin-bottom: 0; } } }

	.specific-gender-data {
		.select_wrapper {
			margin-left: 30px;
			@include m {
				margin-left: 0; } }
		.field--multiple-line {
			&:before {
				left: -36px;
				@include m {
					display: none; } } } }

	.specific-age-data {
		.field--multiple-line {
			&:before {
				left: -15px; } } }

	.specific-income-data {
		.field--multiple-line {
			&:before {
				left: -17px; } } }

	.specific-location-data {
		.field--multiple-line {
			&:before {
				left: -34px; } } }

	.specific-gender-data,
	.specific-age-data,
	.specific-income-data,
	.specific-location-data,
	.specific-title-data {
		display: none; } }






