.upload {
	font-size: 0;
	margin-bottom: -10px;
	@include m {
		margin-bottom: 0; }
	&_first {
		padding-bottom: 9px; }
	&__title {
		@extend .field__title; }
	&__item {
		width: 176px;
		height: 118px;
		background-color: $field-bg;
		//background: red
		border: 2px dashed #bdbdbd;
		border-radius: 5px;
		display: inline-block;
		margin-right: 20px;
		position: relative;
		text-align: center;
		cursor: pointer;
		z-index: 1;
		margin-bottom: 10px;
		&--last {
			margin-right: 0; }
		@include m {
			background-color: $field-bg-m; }
		@include xs {
			margin: 0 auto 20px;
			display: block; }
		//&.error
 }		//	border-color: $red


	&__bg-img {
		@include s(upload-bg);
		display: block;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 10px;
		border: none; }
	&__bg-text {
		color: #bdbdbd;
		font-size: 11px;
		font-weight: 700;
		line-height: 1;
		text-transform: uppercase;
		display: block; }
	&__bg-content {
		display: inline-block;
		@include vcenter;
		z-index: -1; }
	&__preview-img {
		position: absolute;
		left: -2px;
		top: -2px;
		width: calc(100% + 4px);
		height: calc(100% + 4px);
		border: none;
		display: none;
		border-radius: 5px;
		&.is-show {
			display: block; } }
	&__label {
		display: block;
		width: 100%;
		height: 100%;
		cursor: pointer;
		border: none;
		z-index: -1;
		@include nosel; }
	&__btn-reset {
		position: absolute;
		width: 28px;
		height: 28px;
		opacity: 0.8;
		background-color: #e63950;
		border-radius: 3px;
		top: 7px;
		right: 8px;
		display: none;
		&:before {
			content: '';
			@include vcenter;
			@include s(close-upload); }
		&:after {
			content: '';
			position: absolute;
			top: -8px;
			right: -8px;
			left: -8px;
			bottom: -8px; }
		&.is-show {
			display: block; } } }
