.spacel {
	&__slider-wrapper {
		border-radius: 5px;
		overflow: hidden;
		margin-bottom: 30px;
		font-size: 	0;
		@include m {
			width: 280px;
			margin: 0 auto 30px; } }
	&__slide {
		height: 314px;
		@include m {
			height: 280px; }
		a {
			display: block;
			height: 100%; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__main-title {
		//margin-bottom: 20px
		a {
			text-decoration: underline;
			&:hover {
				text-decoration: none; } }
		span {
			&:before {
				left: 109%; }
			&:after {
				right: 109%; } } }
	&__block-title {
		color: $black-sec;
		text-transform: uppercase;
		margin-bottom: 16px;
		letter-spacing: 1px; }
	&__wrapper {
		font-size: 0; }
	&__block {
		@include r(1099) {
			display: block;
			width: 100%; }
		&--border {
			border-bottom: 2px dashed $border-sec;
			padding-bottom: 20px;
			margin-bottom: 19px; }
		&--left, &--right {
			display: inline-block;
			vertical-align: bottom; }
		&--left {
			width: 440*100/724 + %; }
		&--right {} }
	&__more-block {
		margin-top: -8px; }
	&__char-block {
		margin-bottom: 21px;
		&--last {
			margin-bottom: 0; } }
	&__char-inner {
		&--inline {
			display: inline-block;
			vertical-align: top;
			margin-right: 35px; } }
	&__char-title {
		color: $gray-char;
		font-weight: 700;
		text-transform: uppercase; }
	&__char-title, &__char-name, &__char-value, &__more-link {
		line-height: 1.86;
		font-size: 15px; }
	&__char-name {
		color: #333;
		font-weight: 500;
		min-width: 107px;
		display: inline-block;
		vertical-align: top; }
	&__char-value {
		color: #333;
		font-weight: 400; }
	&__more-link {
		color: #00be9d;
		font-weight: 400;
		text-decoration: underline;
		@include m {
			display: block;
			margin-right: 0;
			&:after {
				display: none; } }
		&:hover {
			text-decoration: none; }
		&--first {
			margin-right: 60px;
			position: relative;
			&:after {
				content: '';
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				height: 14px;
				width: 1px;
				background: #828282;
				right: -31px; } } }
	&__info-article {
		color: #333;
		font-size: 15px;
		line-height: 1.86; }
	&__btn-block {
		margin-top: 40px;
		margin-bottom: 40px; }
	&__btn {
		padding: 14px 33px; }
	&__hideblock {
		//font-size: 14px
		//color: #000
		display: none; } }
.scalendar {
	border-radius: 5px;
	box-shadow: 0 6px 22px rgba(123, 139, 149, 0.35);
	background: $white;
	padding: 15px 27px;
	width: 670px;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
	@include m {
		width: 100%;
		padding: 0;
		box-shadow: none;
		background: none; }
	&__title {
		padding-bottom: 5px;
		border-bottom: 1px solid $border-sec;
		margin-bottom: 5px; }
	&__months-line {
		font-size: 0;
		padding-bottom: 5px;
		border-bottom: 1px solid $border-sec;
		min-height: 35px;
		margin-bottom: 20px;
		@include m {
			min-height: 90px; } }

	&__month-link {
		display: inline-block;
		vertical-align: top;
		color: $gray-char;
		font-size: 11px;
		font-weight: 500;
		line-height: 2.545;
		text-transform: uppercase;
		width: 100/12 + %;
		cursor: pointer;
		position: relative;
		@include trans;
		@include m {
			width: 25%;
			transition: none; }
		&:before {
			content: '';
			position: absolute;
			top: -5px;
			bottom: -5px;
			left: 0;
			right: 0; }
		&.is-active {
			color: $blue;
			font-size: 15px;
			font-weight: 700;
			line-height: 1.86; } }
	&__main {
		padding-bottom: 15px;
		border-bottom: 2px dashed $border-sec; }
	&__bottom {
		color: $black-sec;
		font-family: $mont;
		font-weight: 700;
		line-height: 1.69;
		text-transform: uppercase;
		font-size: 16px;
		margin-top: 5px; }
	&__cost-line {
		margin-top: 10px; }
	&__cost-name {
		position: relative;
		bottom: -1px; }
	&__cost-value {
		font-size: 26px;
		line-height: 1.04;
		display: inline-block;
		vertical-align: top; }
	&__bottom-dates {
		text-align: center;
		margin-top: 10px; }
	&__dates-inner {
		display: inline-block;
		vertical-align: top;
		max-width: 90%;
		position: relative;
		padding: 0 15px;
		&:before, &:after {
			position: absolute;
			color: $black-sec;
			top: 50%;
			transform: translateY(-50%); }
		&:before {
			content: '(';
			left: 0; }
		&:after {
			content: ')';
			right: 0; } }

	&__hidden {
		display: none; }
	td {
		border: 1px solid #e1e8ed;
		a, span {
			padding: 13px 5px;
			border-radius: 50%;
			width: 38px;
			height: 38px;
			display: inline-block;
			margin: 6px 0;
			color: $black;
			background: transparent;
			font-size: 13px;
			line-height: 1;
			&.datepick-selected {
				color: $white;
				background: $blue; }
			&.disable {
				background-color: #e1e8ed; } }
		a {
			position: relative;
			&:before {
				content: '';
				position: absolute;
				top: -5px;
				right: -5px;
				bottom: -5px;
				left: -5px; } }
	 &.ui-datepicker-unselectable, .ui-state-disabled {
	 	a {
	 		background-color: #e1e8ed!important;
	 		cursor: default;
	 		color: #555!important; } } }
	.datepick {
		max-width: 616px;
		@include m {
			max-width: 100%; } }
	.datepick-nav, .datepick-month-header {
		display: none; }
	.thead {
		span {
			text-transform: uppercase; } } }
.adm-spacel {
	.spacel__more-link, .spacel__more-link--first {
		@include dt {
			display: block;
			&:after {
				display: none;
				margin-right: 0; } } }
	.spacel__block {
		@include dt {
			display: block;
			width: 100%; } } }
