.home-page {
		//padding-top: 103px
		//+xs
		//	padding-top: 61px
		padding-top: 50px; }
.why-we {
	&__container {
		padding-bottom: 85px;
		border-bottom: 2px solid $border-gray;
		@include xs {
			padding-bottom: 0;
			border-bottom: none; } }
	&__title {
		margin-bottom: 43px;
		@include m {
			margin-bottom: 37px; } }
	&__list {
		text-align: center;
		font-size: 0;
		@include xs {
			padding-bottom: 70px;
			border-bottom: 2px solid $border-gray; } }
	&__item, &__item--last {
		max-width: 315px;
		margin-right: 7%;
		display: inline-block;
		vertical-align: top;
		//&--last
		//	margin-right: 0
		@include dt {
			width: 270px;
			margin-right: 4%; }
		@include m {
			margin-right: 0;
			width: 100%;
			display: block;
			max-width: 100%;
			padding: 0 7px;
			//&--last
 }			//	margin-bottom: 0
		p {
			color: $black;
			font-size: 14px;
			line-height: 1.7; } }
	&__item--last {
		margin-right: 0;
		@include m {
			margin-bottom: 0; } }
	&__img {
		margin-bottom: 24px;
		width: 42px;
		height: 42px;
		margin-left: auto;
		margin-right: auto;
		@include m {
			margin-bottom: 18px; }
		img {
			width: 100%;
			height: 100%;
			object-fit: contain; }
		//svg
 }		//	font-size: 44px
	&__block-title {
		font-size: 18px;
		line-height: 1;
		font-family: $mont;
		font-weight: 400;
		color: $black-dark;
		margin-bottom: 17px;
		display: block; } }
.advertising {
	//padding-top: 81px
	padding-bottom: 56px;
	padding-top: 50px;
	@include m {
		padding-top: 33px;
		padding-bottom: 38px; }
	&__title {
		margin-bottom: 41px;
		@include m {
			margin-bottom: 34px;
			font-size: 25px;
			br {
				display: none; } } }
	&__left {
		img {
			object-fit: contain;
			border-radius: 8px 8px 8px 8px;
			width: 100%;
			height: 100%;
			box-shadow: 0px 15px 50px rgba(0, 0, 0, 0.3); } }
	&__wrapper {
		@include clr; }
	&__left {
		float: left;
		width: percentage(675/1170);
		@include m {
			float: none;
			width: 70%;
			margin-left: auto;
			margin-right: auto; }
		@include r(600) {
			width: 95%; } }
	&__right {
		padding-top: 82px;
		float: right;
		width: percentage(495/1170);
		padding-left: 29px;
		@include dt {
			padding-top: 35px; }
		@include m {
			float: none;
			width: 100%;
			padding-left: 0;
			text-align: center;
			padding-top: 30px; } }
	&__main-text {
		font-weight: 500;
		margin-bottom: 26px; }
	&__sec-text {
		font-weight: 400;
		margin-bottom: 34px; } }
.tile {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 40px;
	.article {
		p {
			color: $white;
			font-size: 15px; } }
	&__item {
		//display: inline-block
		flex-basis: 50%;
		//vertical-align: top
		@include t {
			//display: block
			//width: 100%
			flex-basis: 100%; }
		&--img-r, &--img-l {
			//height: 532px
			@include cover;
			@include t {
				height: auto;
				background: none; }
			img {
				width: 100%;
				height: 100%;
				//object-fit: contain
				@include tmin {
					display: none; } } }
		&--img-l {
			background-position: 90%  50%;
			@include t {
				order: 3; } }
		&--img-r {
			background-position: 50%  0%;
			@include t {
				order: 1; } }
		&--left-text, &--right-text {
			//min-height: 507px
			padding: 30px 4% 0px;
			@include r(1800) {
				padding: 30px 3% 0; }
			@include r(1549) {
				padding: 20px 2% 0; }
			@include r(1200) {
				padding: 20px 15px 0; }
			@include t {
				padding: 20px 15px 20px;
				min-height: auto; } }
		&--left-text {
			//vertical-align: bottom
			//margin-top: 20px
			background-image: linear-gradient(45deg, #01be9d 0%, #0057b8 100%);
			@include t {
				order: 2; } }
		&--right-text {
			background-image: linear-gradient(45deg, #0057b8 0%, #01be9d 100%);
			@include t {
				order: 4; } } }
	&__title {
		text-align: left;
		margin-bottom: 15px;
		font-size: 30px; }
	&__list {
		margin-bottom: -7px;
		li {
			text-align: left;
			margin-bottom: 20px;
			position: relative;
			padding-left: 60px;
			@include clr;
			font-size: 15px;
			color: $white;
			@include r(1549) {
				font-size: 15px; }
			div {
				width: 36px;
				height: 36px;
				border-radius: 50%;
				position: absolute;
				background: #fff;
				float: left;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				&:before {
					content: '';
					@include vcenter;
					@include s(check); } } } } }
.work {
	//padding-top: 81px
	padding-bottom: 85px;
	padding-top: 50px;
	@include m {
		padding-top: 30px;
		padding-bottom: 0; }
	&__title {
		margin-bottom: 26px;
		@include m {
			margin-bottom: 14px; } }
	&__title-sec {
		color: $black;
		font-size: 16px;
		font-weight: 500;
		line-height: 1.75;
		text-align: center;
		margin-bottom: 40px;
		@include m {
			margin-bottom: 26px;
			br {
				display: none; } } }
	&__list {
		font-size: 0;
		text-align: center;
		@include m {
			padding-bottom: 72px;
			border-bottom: 2px solid $border-gray; } }
	&__item {
		display: inline-block;
		width: 25%;
		position: relative;
		padding: 0 7px;
		vertical-align: top;
		&:after {
			content: '';
			position: absolute;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			background-color: #eee;
			height: 165px;
			width: 1px;
			@include m {
				display: none; } }
		&--last {
			&:after {
				display: none; } }
		@include m {
			display: block;
			width: 100%; }
		p {
			font-size: 14px;
			color: $black;
			line-height: 1.7; } }
	&__img {
		margin-bottom: 10px;
		height: 45px;
		img {
			object-fit: contain;
			width: 100%;
			height: 100%; }
		//svg
 } }		//	font-size: 45px
