.user {
	display: inline-block;
	font-size: 0;
	position: relative;
	z-index: 2;
	&__img {
		width: 50px;
		height: 50px;
		background-position: 50% 100%;
		background-color: rgba(23, 23, 25, 0.3);
		background-repeat: no-repeat;
		border-radius: 50%;
		margin-right: 15px;
		display: inline-block;
		@include m {
			margin-right: 0; } }
	&__name {
		font-size: 15px;
		color: $white;
		line-height: 1;
		display: inline-block;
		vertical-align: top;
		margin-top: 17px;
		@include m {
			display: block;
			margin-bottom: 30px; } }
	&__arrow {
		@include s(drop-icon-white);
		position: absolute;
		top: 50%;
		margin-top: -2px;
		right: 26px;
		@include trans;
		@include m {
			display: none; } }
	&__btn {
		color: $white-darken;
		font-size: 15px;
		font-weight: 700;
		line-height: 1;
		text-transform: uppercase;
		position: relative;
		display: inline-block;
		padding: 15px 24px;
		margin-right: 5px;
		&:last-of-type {
			margin-right: 0; }
		@include rmin(1024) {
			&:before {
				@include trans;
				content: '';
				@include coverdiv;
				border: 2px solid white;
				border-radius: 21px;
				opacity: 0;
				transform: translate(-15px, 15px); }
			&:hover {
				&:before {
					transform: translate(0, 0);
					opacity: 1; } } } }
	&__dropbox {
		top: 100%;
		right: 9px;
		width: 187px;
		background: $white;
		@include m {
			position: relative;
			top: 0;
			right: 0;
			display: block;
			width: 100%;
			border-radius: 0;
			box-shadow: none;
			background: none;
			&:before {
				display: none; } } }
	//&__dropbox-inner
	//	+m
	//		background: none
	//		padding: 0
	//		display: inline-block
	//		max-width: 320px
	&__btn-adm {
		width: 100%;
		padding: 15px 20px;
		border-radius: 4px;
		@include trans;
		color: $black;
		font-size: 14px;
		display: block;
		@include tmin {
			&:hover {
				background-color: $green;
				color: $white; } }
		@include m {
			font-size: 13px;
			color: $white;
			text-transform: uppercase;
			border: 2px solid $white;
			border-radius: 28px;
			display: inline-block;
			width: auto;
			min-width: 125px;
			margin-bottom: 15px;
			margin-right: 12px;
			&:nth-of-type(2n) {
				margin-right: 0; } } }
	&.is-open {
		.user__arrow {
			transform: rotateX(180deg); }
		.dropbox {
			display: block; } }
	&.header-adm__user {
		width: 218px;
		background-color: rgba(255, 255, 255, 0.1);
		padding-top: 17px;
		padding-bottom: 14px;
		padding-left: 26px;
		cursor: pointer;
		@include m {
			width: 100%;
			background: none;
			padding: 0; } } }
