.filter {
	width: 270px;
	//float: left
	&__title {
		margin-bottom: 15px;
		@include m {
			text-align: center; } }
	&__block-title {
		color: $black-th;
		font-size: 13px;
		font-weight: 500;
		line-height: 2.15;
		text-transform: uppercase;
		&--spec {
			float: left; } }
	&__inner {
		@include clr;
		margin-bottom: 20px; }
	&__check {
		margin-bottom: 0;
		margin-top: 5px;
		padding-right: 0;
		label {
			padding-left: 30px;
			z-index: 1;
			position: relative;
			cursor: pointer;
			@include nosel; } }
	&__switch {
		float: right;
		position: relative;
		top: -7px; }
	&__block {
		//padding-bottom: 17px
		margin-bottom: 20px;
		&--border {
			border-bottom: 2px dashed #d4dce1; } }
	.select_wrapper {
		margin-right: 0;
		width: 100%;
		min-height: auto; }
	.select-view, .field {
		margin-bottom: 14px; }
	.select-view {
		padding: 8px 35px 8px 22px; }
	.field__input {
		padding: 13px 22px;
		width: 100%; }
	.fields__inner {
		margin-bottom: 18px; }

	&__slider-block {}
	.filter-other-tab-visible {
		display: none; }
	&--small {
		.filter__block-title--spec {
			float: right; }
		.filter__switch {
			float: left; }
		.filter-other-tab-visible {
			display: block; }
		.filter-main-tab-visible {
			display: none; }
		.filter-hidden-other-tab {
			display: none; }
		.filter__block--border-other {
			border-bottom: none; } }
	.select-view {
		min-height: 42px; } }
.slider-block {
	&__inner {
		@include clr;
		position: relative;
		margin-bottom: 23px;
		&:before {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 10px;
			height: 2px;
			background-color: #aaa; } }

	&__input {
		width: 122px;
		height: 46px;
		line-height: 46px;
		font-size: 14px;
		color: $black-th;
		text-align: center;
		background-color: #f3f4f4;
		border-radius: 23px;
		float: left;
		&--right {
			float: right; } }

	&__slider {} }
