.adm-sb {
	width: 268px;
	background: $white;
	box-shadow: 0 15px 32px rgba(0, 0, 0, 0.03);
	padding: 57px 0 0;
	position: fixed;
	height: calc(100vh - 81px);
	left: 0;
	top: 0;
	margin-top: 81px;
	z-index: 1;
	@include m {
		z-index: auto;
		position: static;
		margin-top: 0;
		height: auto;
		width: 100%;
		background: none;
		padding: 0;
		box-shadow: none; }
	&__logo-wrap {
		height: 81px;
		margin-bottom: 57px;
		width: 100%;
		text-align: center;
		background: rgba(45, 54, 63, 0.2);
		padding-top: 13px;
		padding-bottom: 5px;
		position: absolute;
		top: -81px;
		left: 0;
		&:before {
			content: '';
			@include coverdiv;
			background-image: linear-gradient(to right, #185fad 0%, #1868ab 100%);
			z-index: -1; }
		@include m {
			display: none; } }
	&__logo {
		display: inline-block;
		width: 186px;
		img {
			object-fit: contain;
			width: 100%;
			height: 100%; } }
	&__title {
		color: rgba(49, 49, 49, 0.2);
		font-family: $mont;
		font-size: 18px;
		font-weight: 700;
		line-height: 2.44;
		text-transform: uppercase;
		margin-bottom: 13px;
		padding: 0 35px;
		@include m {
			margin-top: 10px;
			padding: 0;
			margin-bottom: 0; } }
	&__copyright {
		color: $gray-light;
		font-size: 13px;
		line-height: 1;
		position: absolute;
		//bottom: 125px
		bottom: 40px;
		left: 0;
		width: 100%;
		text-align: center;
		@include m {
			display: none; } }
	&__view-block {
		display: none;
		@include m {
			display: block;
			background-color: $blue;
			border-radius: 5px;
			//box-shadow: 0 6px 22px rgba(167, 183, 193, 0.2)
			padding: 18px 40px 18px 20px;
			color: $white;
			font-size: 16px;
			line-height: 1;
			position: relative;
			&:before {
				content: '';
				position: absolute;
				@include trans;
				top: 50%;
				transform: translateY(-50%);
				right: 20px;
				@include s(sb-drop-arrow); } } }
	&__mob-menu {
		position: relative;
		@include m {
			margin-bottom: 40px;
			&.is-open {
				.adm-sb__dropbox {
					display: block; }
				.adm-sb__view-block {
					&:before {
						transform: rotateX(180deg); } } } } }

	&__dropbox {
		@include m {
			display: none;
			background-color: $blue;
			position: absolute;
			z-index: 2;
			left: 0;
			width: 100%;
			top: 100%;
			padding: 12px;
			border-radius: 5px;
			margin-top: 18px;
			&:before {
				content: '';
				right: 17px;
				position: absolute;
				@include arr(17, 7, $blue, t);
				top: -7px; } } } }

.adm-sb-link {
	position: relative;
	padding: 22px 70px 22px 80px;
	display: block;
	@include m {
		padding: 17px 40px 17px 60px;
		border-radius: 4px; }

	&:after {
		content: '';
		position: absolute;
		bottom: 0;
		width: 74%;
		background: $border-gray;
		height: 1px;
		left: 50%;
		transform: translateX(-50%);
		@include m {
			display: none; } }
	span {
		color: #033;
		line-height: 1;
		font-size: 16px;
		@include m {
			color: $white; } }
	svg {
		fill: #033;
		font-size: 16px;
		@include trans;
		position: absolute;
		left: 35px;
		top: 22px;
		@include m {
			top: 16px;
			fill: $white;
			left: 16px; } }
	&__border {
		@include trans;
		position: absolute;
		left: 0;
		top: 0;
		width: 0px;
		height: 100%;
		opacity: 0; }
	@include mmin {
		&:hover {
			background-color: rgba(0, 130, 249, 0.03);
			.adm-sb-link__border {
				background-color: $blue;
				box-shadow: 0 0 12px $blue;
				opacity: 1;
				width: 4px; } } }
	&--active {
		@include m {
			background: $blue-dark; }
		span {
			color: $blue; }
		svg {
			fill: $blue; } }
	&__message {
		position: absolute;
		right: 38px;
		width: 24px;
		height: 24px;
		background-color: $blue;
		text-align: center;
		line-height: 24px;
		font-size: 10px;
		color: $white;
		border-radius: 50%;
		top: 50%;
		transform: translateY(-50%);
		@include m {
			background-color: $white;
			color: $blue; } } }
